.company-page-awards {
  padding: 150px 30px 140px;
  position: relative;
}
.company-page-awards .gradient {
    position: absolute;
    bottom: 0;
    right: 0;
    left: 0;
    top: 0;
    background: linear-gradient(180deg, #061123 0%, #0e2647 100%);
}
.company-page-awards .titles {
    text-align: center;
    max-width: 960px;
    margin: auto;
    margin-bottom: 80px;
}
.company-page-awards .titles h5 {
      text-transform: uppercase;
      letter-spacing: 2px;
      font-weight: 500;
      font-size: 18px;
      color: #e70040;
      margin: 0;
}
.company-page-awards .titles h3 {
      font-size: 60px;
      margin: 30px 0;
      color: #ffffff;
}
.company-page-awards .awards-grid {
    grid-template-columns: repeat(4, 1fr);
    list-style-type: none;
    grid-gap: 25px;
    display: grid;
    padding: 0;
    margin: 0;
}
.company-page-awards .award-block {
    position: relative;
    text-align: center;
    padding: 30px;
}
.company-page-awards .award-block .award-logo {
      margin: auto;
      background-repeat: no-repeat;
      background-position: center;
      background-size: contain;
      padding-bottom: 45%;
      margin-bottom: 10px;
}
.company-page-awards .in-view .award-block {
    transition: opacity 600ms, transform 600ms;
    transition-delay: 400ms;
}
.company-page-awards .in-view .award-block:nth-child(16n + 1) {
    transition-delay: 475ms;
}
.company-page-awards .in-view .award-block:nth-child(16n + 2) {
    transition-delay: 550ms;
}
.company-page-awards .in-view .award-block:nth-child(16n + 3) {
    transition-delay: 625ms;
}
.company-page-awards .in-view .award-block:nth-child(16n + 4) {
    transition-delay: 700ms;
}
.company-page-awards .in-view .award-block:nth-child(16n + 5) {
    transition-delay: 775ms;
}
.company-page-awards .in-view .award-block:nth-child(16n + 6) {
    transition-delay: 850ms;
}
.company-page-awards .in-view .award-block:nth-child(16n + 7) {
    transition-delay: 925ms;
}
.company-page-awards .in-view .award-block:nth-child(16n + 8) {
    transition-delay: 1000ms;
}
.company-page-awards .in-view .award-block:nth-child(16n + 9) {
    transition-delay: 1075ms;
}
.company-page-awards .in-view .award-block:nth-child(16n + 10) {
    transition-delay: 1150ms;
}
.company-page-awards .in-view .award-block:nth-child(16n + 11) {
    transition-delay: 1225ms;
}
.company-page-awards .in-view .award-block:nth-child(16n + 12) {
    transition-delay: 1300ms;
}
.company-page-awards .in-view .award-block:nth-child(16n + 13) {
    transition-delay: 1375ms;
}
.company-page-awards .in-view .award-block:nth-child(16n + 14) {
    transition-delay: 1450ms;
}
.company-page-awards .in-view .award-block:nth-child(16n + 15) {
    transition-delay: 1525ms;
}
.company-page-awards .in-view .award-block:nth-child(16n + 16) {
    transition-delay: 1600ms;
}
.company-page-awards .below-view .award-block {
    transform: translateY(10%);
    opacity: 0;
}
@media only screen and (max-width: 790px) {
.company-page-awards {
    padding-right: 16px;
    padding-left: 16px;
    padding-bottom: 60px;
    padding-top: 70px;
}
.company-page-awards .dotted {
      display: none;
}
.company-page-awards .titles {
      margin-bottom: 40px;
}
.company-page-awards .titles h5 {
        font-size: 18px;
}
.company-page-awards .titles h3 {
        font-size: 28px;
        margin: 20px 0;
}
.company-page-awards .awards-grid {
      grid-template-columns: 1fr 1fr;
      grid-gap: 16px;
}
.company-page-awards .award-block {
      padding: 20px 15px;
}
.company-page-awards .award-block .shared-fading-border {
        -webkit-mask-image: linear-gradient(0deg, black 40%, rgba(0, 0, 0, 0) 120%);
        mask-image: linear-gradient(0deg, black 40%, rgba(0, 0, 0, 0) 120%);
}
}
