.journey-svg-hydra {
  transition: opacity 0.4s;
}
.journey-svg-hydra.paused {
    opacity: 0 !important;
}
.journey-svg-hydra .line-wrap {
    animation: scan 4s infinite, hydra-pulse 4s infinite;
}
.journey-svg-hydra .full-line {
    transform: rotate(-60deg) skewY(29.5deg);
    transform-origin: 560px 0;
}
.journey-svg-hydra .hydra-line {
    transform: scale(0.6) translate(370px, 0);
}
.journey-svg-hydra .gradient {
    animation: gradient-pulse 4s infinite;
}
.journey-svg-hydra .hydra-bubble {
    transform: translate(-7px, -11px);
}
.journey-svg-hydra .hydra-text {
    transform: translate(-7px, -11px);
    animation: hydra-pulse 4s infinite;
}
.journey-svg-hydra .st1 {
    fill: none;
    stroke: #ff3655;
    stroke-width: 2;
    stroke-miterlimit: 10;
}
.journey-svg-hydra .st2 {
    opacity: 0.4;
    fill: none;
    stroke: #ff3655;
    stroke-width: 0.5;
    stroke-miterlimit: 10;
}
.journey-svg-hydra .st3 {
    opacity: 0.3;
    fill: #ff3655;
}
.journey-svg-hydra .st4 {
    fill: none;
    stroke: #ff3655;
    stroke-miterlimit: 10;
}
.journey-svg-hydra .st5 {
    enable-background: new;
}
.journey-svg-hydra .st7 {
    fill: #ff3655;
}
@keyframes scan {
0% {
    transform: translate(465px, 427px);
}
100% {
    transform: translate(-269px, 4px);
}
}
@keyframes hydra-pulse {
0% {
    opacity: 0;
}
10% {
    opacity: 0;
}
15% {
    opacity: 1;
}
40% {
    opacity: 1;
}
48% {
    opacity: 0;
}
100% {
    opacity: 0;
}
}
@keyframes gradient-pulse {
0% {
    transform: scaleY(0.5);
}
25% {
    transform: scaleY(1);
}
51% {
    transform: scaleY(0.5);
}
100% {
    transform: scaleY(1);
}
}
