.partner-program-partner-grid {
  padding: 0 30px;
  max-width: 1140px;
  margin: 180px auto 170px;
}
.page-id-7339 .partner-program-partner-grid {
    display: none;
}
.partner-program-partner-grid .intro {
    font-size: 60px;
    text-align: center;
}
@media (max-width: 750px) {
.partner-program-partner-grid .intro {
        font-size: 36px;
}
}
.partner-program-partner-grid .logo-grid {
    margin: 0;
    padding: 0;
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    grid-gap: 25px;
    list-style: none;
}
@media (max-width: 900px) {
.partner-program-partner-grid .logo-grid {
        grid-template-columns: repeat(2, 1fr);
}
}
@media (max-width: 750px) {
.partner-program-partner-grid .logo-grid {
        grid-gap: 10px;
}
}
.partner-program-partner-grid .logo-grid .single-logo {
      max-width: 260px;
      height: 145px;
      display: flex;
      align-items: center;
      justify-content: center;
      background: white;
      border-radius: 12px;
      border: 1px solid #dfe5f0;
      align-self: center;
      justify-self: center;
}
.partner-program-partner-grid .logo-grid .single-logo img {
        width: 66%;
        height: auto;
        margin: auto;
        display: block;
}
