.resource-block {
  color: #00193e;
  text-decoration: none;
  display: flex;
  flex-direction: column;
}
.resource-block .content-wrap {
    flex: 1;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}
.resource-block .content-wrap .title {
      font-size: 20px;
      line-height: 1.5;
      margin: 15px 0;
}
.resource-block .content-wrap .link svg {
      position: relative;
      top: 2px;
}
.resource-block .content-wrap .cta-label {
      color: #106bff;
      font-size: 16px;
      margin-left: 5px;
}
