.company-page-slide {
  --slide-highlight-color: #0b1221;
  box-shadow: 0 12px 34px 0 rgba(0, 25, 62, 0.14);
  background-color: #ffffff;
  box-sizing: border-box;
  text-decoration: none;
  padding: 110px 100px;
  position: relative;
  min-width: 750px;
  display: block;
  height: 500px;
  width: 60%;
}
.company-page-slide:not(.has-content) {
    padding: 0;
}
.company-page-slide:not(.has-content) .images {
      -webkit-clip-path: none !important;
      clip-path: none !important;
}
.company-page-slide .images {
    -webkit-clip-path: polygon(0 0, 100% 0, 100% 100%, 67% 29%);
    clip-path: polygon(0 0, 100% 0, 100% 100%, 67% 29%);
    position: absolute;
    bottom: 0;
    right: 0;
    left: 0;
    top: 0;
}
.company-page-slide .overlay {
    transition: background 250ms ease;
    background-color: rgba(0, 0, 0, 0.25);
    position: absolute;
    bottom: 0;
    right: 0;
    left: 0;
    top: 0;
}
.not-mobile .company-page-initiatives:not(.hide-filter) .company-page-slide:hover .images,
  .not-mobile .company-page-initiatives:not(.hide-filter) .company-page-slide:focus .images {
    filter: url(#shared-glitch-filter);
}
.company-page-slide .link-icon {
    transition: transform 200ms cubic-bezier(0.445, 0.05, 0.55, 0.95);
    position: absolute;
    padding: 35px;
    right: 0;
    top: 0;
}
.company-page-slide:hover .link-icon {
    transform: translate(3px, -3px);
}
.company-page-slide .slide-content {
    padding: 110px 100px;
    position: absolute;
    line-height: 1.6;
    color: #8694af;
    width: 58%;
    bottom: 0;
    left: 0;
}
.company-page-slide .slide-content h3 {
      font-weight: 500;
      font-size: 40px;
      line-height: 1;
      color: #0b1221;
      margin: 0;
}
.company-page-slide .slide-content h4 {
      font-family: "Graphik";
      font-weight: 400;
      font-size: 24px;
      margin: 15px 0;
      color: #0b1221;
      color: var(--slide-highlight-color);
}
.company-page-slide .slide-content > *:last-child {
      margin-bottom: 0;
}
@media only screen and (max-width: 1400px) {
.company-page-slide .images {
    -webkit-clip-path: polygon(0 0, 100% 0, 100% 100%, 67% 29%);
    clip-path: polygon(0 0, 100% 0, 100% 100%, 77% 19%);
}
}
