.shared-fading-border {
  background: linear-gradient(to bottom, rgba(11, 86, 99, 0.35) 0%, rgba(11, 86, 99, 0.05) 100%);
  border: 1px solid #0b5663;
  border-radius: 10px;
  position: absolute;
  bottom: 0;
  right: 0;
  left: 0;
  top: 0;
  -webkit-mask-image: linear-gradient(to top, black 40%, rgba(0, 0, 0, 0.5) 100%);
  mask-image: linear-gradient(to top, black 40%, rgba(0, 0, 0, 0.5) 100%);
}
