body.page-template-template-company #app {
  background-color: #0b1221;
  min-height: 100vh;
}
.company-page-initiatives {
  padding: 100px 0 140px;
  position: relative;
  overflow: hidden;
}
.company-page-initiatives .gradient {
    position: absolute;
    bottom: 0;
    right: 0;
    left: 0;
    top: 0;
    background: linear-gradient(180deg, white 0%, #eef5ff 100%);
}
.company-page-initiatives .information {
    padding-right: 30px;
    padding-left: 30px;
    margin-bottom: 80px;
}
.company-page-initiatives .information .title {
      text-transform: uppercase;
      letter-spacing: 2px;
      font-weight: 500;
      font-size: 18px;
      color: #e70040;
      margin: 0;
      margin-bottom: 35px;
}
.company-page-initiatives .information .info-wrap {
      font-size: 20px;
      color: #8694af;
      display: flex;
      flex-direction: column;
      padding: 0 50px;
}
.company-page-initiatives .information .info-wrap > * {
        margin: 0;
}
.company-page-initiatives .information .info-wrap h3 {
        line-height: 1;
        font-size: 60px;
        color: #0b1221;
        text-align: left;
}
.company-page-initiatives .information .info-wrap p {
        line-height: 1.5;
        margin-top: 15px;
}
.company-page-initiatives .controls {
    transition: opacity 200ms;
    position: absolute;
    margin-left: -50px;
    display: flex;
    left: 100%;
    bottom: 0;
}
.company-page-initiatives .controls .nav {
      margin: 0 8px;
}
.company-page-initiatives .controls path,
    .company-page-initiatives .controls circle {
      transition: fill 250ms, stroke 250ms;
}
.company-page-initiatives .controls .nav:disabled {
      cursor: initial;
}
.company-page-initiatives .controls .nav:disabled svg path {
      fill: #c9d5e6;
}
.company-page-initiatives .controls .nav:disabled svg circle {
      stroke: #c9d5e6;
}
.company-page-initiatives .initiatives-carousel {
    position: relative;
}
.company-page-initiatives .initiatives-carousel:focus {
      outline: none;
}
.company-page-initiatives .carousel-placeholder,
  .company-page-initiatives .company-page-slide {
    margin-right: 30px;
}
.company-page-initiatives .carousel-placeholder {
    background-image: url("images/patterns/dots.png");
    background-size: 10px;
    min-width: 750px;
    opacity: 0.75;
    height: 500px;
    width: 60%;
}
@media only screen and (max-width: 1500px) {
.company-page-initiatives .controls {
    justify-content: flex-end;
    margin-top: 40px;
    position: static;
}
.company-page-initiatives .information {
    margin-bottom: 40px;
}
}
@media only screen and (max-width: 790px) {
.company-page-initiatives {
    padding-bottom: 60px;
    padding-top: 70px;
}
.company-page-initiatives .contained {
      padding-right: 16px;
      padding-left: 16px;
}
.company-page-initiatives .information .title {
      margin-bottom: 0;
}
.company-page-initiatives .information .info-wrap {
      flex-direction: column;
}
.company-page-initiatives .information .info-wrap > * {
        width: 100%;
}
.company-page-initiatives .information .info-wrap p {
        padding-right: 0;
        padding-left: 0;
}
.company-page-initiatives .information .info-wrap h3 {
      font-size: 32px;
      margin: 30px 0;
}
.company-page-initiatives .initiatives-carousel {
      padding-right: 16px;
      padding-left: 16px;
}
.company-page-initiatives .initiatives-carousel .carousel-placeholder {
        display: none;
}
.company-page-initiatives .initiatives-carousel .company-page-slide {
        margin-bottom: 25px;
        min-width: initial;
        padding: 50px 30px;
        padding-bottom: 115px;
        height: auto;
        width: 100%;
}
.company-page-initiatives .initiatives-carousel .images {
        top: initial;
        height: 115px;
        -webkit-clip-path: polygon(100% 0, 0% 100%, 100% 100%);
        clip-path: polygon(100% 0, 0% 100%, 100% 100%);
}
.company-page-initiatives .initiatives-carousel .company-page-slide .slide-content {
        margin-right: initial;
        width: initial;
        position: relative;
        bottom: initial;
        right: initial;
        left: initial;
        top: initial;
        padding: 0;
}
.company-page-initiatives .initiatives-carousel .company-page-slide .link-icon {
        display: none;
}
}
