.products-results.contained {
  margin-top: -400px;
}
.products-results.contained .lines-fade {
    display: flex;
    justify-content: space-between;
    max-width: 800px;
    height: 180px;
    margin: auto;
    position: absolute;
    right: 0;
    bottom: 100%;
    left: 0;
}
.products-results.contained .lines-fade .line.vertical {
      background: linear-gradient(to bottom, transparent, rgba(228, 229, 233, 0.4));
}
.products-results.contained .line {
    background: rgba(228, 229, 233, 0.4);
    height: 1px;
}
.products-results.contained .line.horizontal {
      flex: 1;
}
.products-results.contained .line.vertical {
      width: 1px;
      height: 100%;
      background: linear-gradient(to bottom, rgba(228, 229, 233, 0.4) 0%, rgba(228, 229, 233, 0.4) calc(50% + 0.5px), transparent calc(50% + 0.5px), transparent 100%);
}
.products-results.contained .title-wrap {
    display: grid;
    grid-template-columns: 1fr auto 1fr;
    grid-column-gap: 60px;
    max-width: 800px;
    margin: auto;
}
.products-results.contained .title-wrap .line-wrap {
      display: flex;
      align-items: center;
}
.products-results.contained .title-wrap .bullseye use {
      fill: rgba(228, 229, 233, 0.4);
}
.products-results.contained .title-wrap .bullseye circle:nth-child(2) {
      stroke: rgba(228, 229, 233, 0.4);
}
.products-results.contained .title {
    font-size: 60px;
    margin: 0;
    text-align: center;
    color: #ffffff;
}
.products-results.contained .fh-slideshow {
    background-color: #f1f1f1;
    border-radius: 5px;
}
.products-results.contained .slideshow-wrap {
    position: relative;
    width: calc(100% - 80px);
    margin: auto;
    padding-bottom: 100px;
}
.products-results.contained .slideshow-wrap .pagination {
      display: flex;
      justify-content: center;
      font-family: "Crique Grotesk";
      text-transform: uppercase;
      font-size: 14px;
      letter-spacing: 2px;
      font-weight: 400;
      margin-bottom: 40px;
      position: absolute;
      bottom: 100%;
      width: 100%;
      text-align: center;
}
.products-results.contained .slideshow-wrap .pagination .pagination-item {
        position: relative;
        white-space: nowrap;
        text-align: center;
}
.products-results.contained .slideshow-wrap .pagination .pagination-item span {
          position: relative;
}
.products-results.contained .slideshow-wrap .pagination .pagination-item span::after {
            content: '';
            position: absolute;
            height: 2px;
            top: calc(100% + 6px);
            right: 0;
            left: 0;
            background: #ff3655;
            transform-origin: center left;
            transition: transform 0.4s;
            transform: scale(0, 1);
}
.products-results.contained .slideshow-wrap .pagination .pagination-item button {
          text-transform: uppercase;
          letter-spacing: 1px;
          color: #7a72b1;
          transition: color 0.3s;
          position: relative;
}
.products-results.contained .slideshow-wrap .pagination .pagination-item + .pagination-item {
        margin-left: 40px;
}
.products-results.contained .slideshow-wrap .pagination .pagination-item.active span::after {
        transform: scale(1);
}
.products-results.contained .slideshow-wrap .pagination .pagination-item.active button {
        color: #ffffff;
}
.not-mobile .products-results.contained .slideshow-wrap .pagination .pagination-item:hover button,
      .not-mobile .products-results.contained .slideshow-wrap .pagination .pagination-item:focus button {
        color: #ffffff;
}
.products-results.contained .slideshow-wrap .fh-slideshow {
      margin-top: 95px;
      display: flex;
      flex-direction: column;
      overflow: visible;
}
.products-results.contained .slideshow-wrap .fh-slideshow .nav {
        position: absolute;
        top: 0;
        bottom: 0;
        width: 60px;
        height: 60px;
        margin: auto;
        z-index: 5;
        background: #ffffff;
        border-radius: 50%;
        box-shadow: rgba(0, 0, 0, 0.1) 0 12px 34px;
        transition: box-shadow 0.3s;
}
.products-results.contained .slideshow-wrap .fh-slideshow .nav circle {
          stroke: transparent;
}
.products-results.contained .slideshow-wrap .fh-slideshow .nav.prev {
          left: -30px;
}
.products-results.contained .slideshow-wrap .fh-slideshow .nav.next {
          right: -30px;
}
.not-mobile .products-results.contained .slideshow-wrap .fh-slideshow .nav:hover,
        .not-mobile .products-results.contained .slideshow-wrap .fh-slideshow .nav:focus {
          box-shadow: rgba(0, 0, 0, 0.05) 0 12px 34px;
}
.products-results.contained .slideshow-wrap .fh-slideshow .slide-wrap {
        order: 1;
        box-shadow: rgba(0, 25, 62, 0.2) 0 12px 34px;
        position: relative;
        width: 100%;
        height: 0;
        padding-bottom: 56.25%;
}
.products-results.contained .slideshow-wrap .fh-slideshow .slide-wrap.fade-leave-active {
          position: absolute;
}
.products-results.contained .slideshow-wrap .fh-slideshow .slide-wrap .slide {
          position: absolute;
          bottom: 0;
          right: 0;
          left: 0;
          top: 0;
}
.products-results.contained .slideshow-wrap .fh-slideshow .slide-wrap .responsive-image {
          border-radius: 5px;
          overflow: hidden;
}
.products-results.contained .slideshow-wrap .fh-slideshow .slide-wrap .content-wrap {
          position: absolute;
          right: 0;
          bottom: 0;
          width: 60%;
          max-height: 80%;
          overflow-y: auto;
          filter: drop-shadow(-10px -10px 25px rgba(0, 0, 0, 0.2));
}
.products-results.contained .slideshow-wrap .fh-slideshow .slide-wrap .content-wrap .clip-wrap {
            background: #ffffff;
            padding: 55px 40px 65px;
            clip-path: polygon(30px 0, 100% 0, 100% 100%, 0 100%, 0 30px);
}
.products-results.contained .slideshow-wrap .fh-slideshow .slide-wrap .content-wrap .title {
            color: #000000;
            font-size: 24px;
            margin: 0;
            text-align: left;
            text-transform: uppercase;
            letter-spacing: 2px;
}
.products-results.contained .slideshow-wrap .fh-slideshow .slide-wrap .content-wrap .wp-content {
            font-size: 18px;
            color: #8695ae;
            line-height: 1.5;
}
.products-results.contained .slideshow-wrap .fh-slideshow .slide-wrap .content-wrap .wp-content strong {
              font-weight: 400;
              color: #ff3655;
              text-transform: uppercase;
              font-size: 16px;
              letter-spacing: 1px;
}
.products-results.contained .slideshow-wrap .fh-slideshow .slide-wrap .content-wrap .wp-content p {
              margin: 40px 0 0;
}
@media only screen and (max-width: 790px) {
.products-results.contained {
      overflow: hidden;
      margin-top: -120px;
}
.products-results.contained .title {
        font-size: 30px;
}
.products-results.contained .line,
      .products-results.contained .bullseye {
        display: none;
}
.products-results.contained .slideshow-wrap {
        width: 100%;
        padding: 0 16px 50px;
        box-sizing: border-box;
        overflow: hidden;
}
.products-results.contained .slideshow-wrap .pagination {
          text-align: center;
          margin-left: 50%;
          justify-content: flex-start;
          transform: translateX(calc(-230px * var(--index)));
          transition: transform 0.4s;
          width: initial;
          margin-bottom: 30px;
}
.products-results.contained .slideshow-wrap .pagination .pagination-item {
            display: inline-block;
            white-space: nowrap;
            width: 230px;
            transform: translateX(-50%);
}
.products-results.contained .slideshow-wrap .pagination .pagination-item + .pagination-item {
            margin-left: 0;
}
.products-results.contained .slideshow-wrap .fh-slideshow {
          margin-top: 65px;
}
.products-results.contained .slideshow-wrap .fh-slideshow .slide-wrap {
          box-shadow: none;
}
.products-results.contained .slideshow-wrap .fh-slideshow .slide-wrap .image-sizer {
            border-radius: 5px;
            box-shadow: rgba(0, 25, 62, 0.2) 0 12px 14px;
}
.products-results.contained .slideshow-wrap .nav {
          display: none;
}
.products-results.contained .slide-content-wrap {
        position: relative;
        width: 100%;
        background: transparent;
        box-shadow: none;
        text-align: center;
}
.products-results.contained .slide-content-wrap .title {
          color: #000000;
          font-size: 24px;
          margin: 0;
          text-align: left;
          text-transform: uppercase;
          letter-spacing: 2px;
}
.products-results.contained .slide-content-wrap .wp-content {
          font-size: 18px;
          color: #8695ae;
          line-height: 1.5;
}
.products-results.contained .slide-content-wrap .wp-content strong {
            font-weight: 400;
            color: #ff3655;
            text-transform: uppercase;
            font-size: 16px;
            letter-spacing: 1px;
}
.products-results.contained .slide-content-wrap .wp-content p {
            margin: 40px 0 0;
}
.products-results.contained .slide-content-wrap .clip-wrap {
          background: transparent;
          padding: 35px 10px 0;
}
.products-results.contained .slide-content-wrap .title {
          text-align: center;
          font-size: 18px;
}
.products-results.contained .slide-content-wrap .wp-content > p:first-child {
          margin-top: 15px;
}
.products-results.contained .slide-content-wrap .wp-content p {
          font-size: 16px;
}
.products-results.contained .slide-content-wrap .wp-content p strong {
            font-size: 14px;
}
}
