.resource-related {
  padding: 0 30px 80px;
}
.resource-related > .title {
    font-size: 48px;
    margin-bottom: 30px;
}
.resource-related .list {
    padding: 0;
    margin: 0;
    list-style: none;
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    grid-column-gap: 25px;
}
.resource-related .related-link {
    font-size: 20px;
    font-weight: 700;
    line-height: 1.5;
    text-decoration: none;
    color: #00193e;
}
.resource-related .related-link .post-title {
      margin-bottom: 10px;
}
.resource-related .related-link .cta-label {
      color: #0f61ff;
      font-weight: 400;
      font-size: 16px;
}
@media only screen and (max-width: 790px) {
.resource-related {
      padding: 0 16px 80px;
}
.resource-related .list {
        grid-template-columns: 1fr;
        grid-row-gap: 20px;
}
.resource-related .related-link .post-title {
        margin: 10px 0 5px;
}
}
