body.page-template-template-partner-program .view-partner-program {
  background: linear-gradient(to bottom, transparent, #eef4ff);
}
body.page-template-template-partner-program .view-partner-program .title {
    line-height: 0.78;
}
body.page-template-template-partner-program .small-title {
  text-align: center;
  font-size: 60px;
  margin: 0 30px;
}
@media (max-width: 800px) {
body.page-template-template-partner-program .small-title {
      font-size: 30px;
}
}
body.page-template-template-partner-program .end-link-wrap {
  text-align: center;
}
body.page-template-template-partner-program .end-link-wrap .end-link {
    text-decoration: none;
    padding: 24px 50px 25px;
    font-size: 16px;
    color: #0b1221;
    background: #ffffff;
    border-radius: 45px;
    display: inline-block;
    font-weight: 500;
    transition: border 0.3s;
    white-space: nowrap;
    transition: opacity 0.3s;
}
body.page-template-template-partner-program .end-link-wrap .end-link:not(.primary) {
      background: #0f61ff;
      color: #ffffff;
      border: 3px solid rgba(15, 97, 255, 0.3);
      padding-bottom: 23px;
      padding-top: 22px;
}
.not-mobile body.page-template-template-partner-program .end-link-wrap .end-link:not(.primary):hover,
      .not-mobile body.page-template-template-partner-program .end-link-wrap .end-link:not(.primary):focus {
        border-color: transparent;
}
body.page-template-template-partner-program .end-link-wrap .end-link + .shortcode-button {
      margin-left: 20px;
}
@media only screen and (max-width: 790px) {
body.page-template-template-partner-program .end-link-wrap .end-link {
        font-size: 14px;
        padding-right: 35px;
        padding-left: 35px;
}
}
body.page-template-template-partner-program .end-link-wrap .end-link:hover, body.page-template-template-partner-program .end-link-wrap .end-link:focus {
      opacity: 0.7;
}
body.page-template-template-partner-program .existing-partners {
  background: #0f61ff;
  text-align: center;
  color: #ffffff;
  padding: 95px 30px;
  margin-top: 95px;
  font-size: 20px;
  font-weight: 700;
}
@media (max-width: 850px) {
body.page-template-template-partner-program .existing-partners {
      font-size: 18px;
}
}
body.page-template-template-partner-program .existing-partners img {
    width: 215px;
    height: auto;
}
body.page-template-template-partner-program .existing-partners h3 {
    font-size: 60px;
}
@media (max-width: 850px) {
body.page-template-template-partner-program .existing-partners h3 {
        font-size: 40px;
}
}
body.page-template-template-partner-program .existing-partners p {
    max-width: 900px;
    line-height: 1.5;
    margin-right: auto;
    margin-left: auto;
}
