.partner-child-breakout {
  background: #071124;
  color: #ffffff;
  display: flex;
  align-items: center;
  position: relative;
  overflow: hidden;
}
.partner-child-breakout .waves-decoration {
    position: absolute;
    bottom: 0;
    right: 0;
    left: 0;
    top: 0;
    background: url("images/patterns/wave.png");
    background-size: 100px;
    opacity: 0.07;
}
.partner-child-breakout .image-wrap {
    position: absolute;
    top: 80px;
    right: -40vw;
    bottom: 0;
    left: 60vw;
    margin: auto;
}
.partner-child-breakout .image-wrap .background-logo,
    .partner-child-breakout .image-wrap .background-logo-glow {
      height: auto;
      width: 100%;
      object-position: center;
      object-fit: contain;
}
.partner-child-breakout .image-wrap .background-logo-glow {
      top: 0;
      left: 0;
      position: absolute;
      opacity: 0.5;
      animation: glow 5s infinite;
}
.partner-child-breakout .content-wrap {
    max-width: 1200px;
    margin: 0 auto;
    padding: 80px 20px 100px;
    position: relative;
    width: 100%;
}
.partner-child-breakout .content-wrap .wp-content > * {
      max-width: 600px;
      margin-left: 0;
      transition: transform 0.4s, opacity 0.4s;
}
.page-id-7339 .partner-child-breakout .content-wrap .wp-content > * {
        margin-right: auto;
        margin-left: auto;
}
.partner-child-breakout .content-wrap .wp-content > *:nth-child(1) {
        transition-delay: 0.1s;
}
.partner-child-breakout .content-wrap .wp-content > *:nth-child(2) {
        transition-delay: 0.2s;
}
.partner-child-breakout .content-wrap .wp-content > *:nth-child(3) {
        transition-delay: 0.3s;
}
.partner-child-breakout .content-wrap .wp-content > *:nth-child(4) {
        transition-delay: 0.4s;
}
.partner-child-breakout .content-wrap .wp-content > *:nth-child(5) {
        transition-delay: 0.5s;
}
.partner-child-breakout .content-wrap .wp-content > *:nth-child(6) {
        transition-delay: 0.6s;
}
.partner-child-breakout .content-wrap .wp-content > *:nth-child(7) {
        transition-delay: 0.7s;
}
.partner-child-breakout .content-wrap .wp-content > *:nth-child(8) {
        transition-delay: 0.8s;
}
.partner-child-breakout .content-wrap .wp-content > *:nth-child(9) {
        transition-delay: 0.9s;
}
.partner-child-breakout .content-wrap .wp-content > *:nth-child(10) {
        transition-delay: 1s;
}
.partner-child-breakout .content-wrap .wp-content > *:nth-child(11) {
        transition-delay: 1.1s;
}
.partner-child-breakout .content-wrap .wp-content > *:nth-child(12) {
        transition-delay: 1.2s;
}
.partner-child-breakout .content-wrap .wp-content > *:nth-child(13) {
        transition-delay: 1.3s;
}
.partner-child-breakout .content-wrap .wp-content > *:nth-child(14) {
        transition-delay: 1.4s;
}
.partner-child-breakout .content-wrap .wp-content > *:nth-child(15) {
        transition-delay: 1.5s;
}
.partner-child-breakout .content-wrap .wp-content.below-view:not(.fired-once) > * {
      transform: translateY(50px);
      opacity: 0;
}
.partner-child-breakout .content-wrap .wp-content p {
      clear: both;
}
.partner-child-breakout .content-wrap .wp-content img.alignleft {
      float: left;
      max-width: 28%;
      margin-right: 40px;
      height: auto;
      clear: both;
}
.partner-child-breakout .content-wrap .wp-content img.alignleft + p {
        float: right;
        margin-top: 0;
        width: 60%;
        clear: initial;
}
.partner-child-breakout .content-wrap .wp-content img.alignleft + p::after {
          content: '';
          clear: both;
}
@media (max-width: 950px) {
.partner-child-breakout .content-wrap .wp-content img.alignleft {
          float: initial;
          max-width: initial;
          margin-right: initial;
}
.partner-child-breakout .content-wrap .wp-content img.alignleft + p {
            float: initial;
            width: initial;
            max-width: initial;
}
}
.partner-child-breakout .content-wrap .wp-content .learn-more-cta {
      text-decoration: none;
      padding: 24px 50px 25px;
      font-size: 16px;
      color: #0b1221;
      background: #ffffff;
      border-radius: 45px;
      display: inline-block;
      font-weight: 500;
      transition: border 0.3s;
      white-space: nowrap;
      transition: opacity 0.3s;
}
.partner-child-breakout .content-wrap .wp-content .learn-more-cta:not(.primary) {
        background: #0f61ff;
        color: #ffffff;
        border: 3px solid rgba(15, 97, 255, 0.3);
        padding-bottom: 23px;
        padding-top: 22px;
}
.not-mobile .partner-child-breakout .content-wrap .wp-content .learn-more-cta:not(.primary):hover,
        .not-mobile .partner-child-breakout .content-wrap .wp-content .learn-more-cta:not(.primary):focus {
          border-color: transparent;
}
.partner-child-breakout .content-wrap .wp-content .learn-more-cta + .shortcode-button {
        margin-left: 20px;
}
@media only screen and (max-width: 790px) {
.partner-child-breakout .content-wrap .wp-content .learn-more-cta {
          font-size: 14px;
          padding-right: 35px;
          padding-left: 35px;
}
}
.partner-child-breakout .content-wrap .wp-content .learn-more-cta:hover, .partner-child-breakout .content-wrap .wp-content .learn-more-cta:focus {
        opacity: 0.7;
}
.partner-child-breakout .content-wrap h5 {
      color: #0f61ff;
      font-size: 16px;
      margin: 0;
      text-transform: uppercase;
      font-weight: 400;
      letter-spacing: 2px;
}
.partner-child-breakout .content-wrap h3 {
      margin: 15px auto 0;
      font-size: 60px;
}
.partner-child-breakout .content-wrap h3 em {
        font-style: normal;
        color: #777;
}
.partner-child-breakout .content-wrap p {
      font-size: 18px;
      margin-top: 30px;
      line-height: 1.4;
      color: #8696ad;
}
.partner-child-breakout .content-wrap p em {
        font-style: normal;
        text-transform: uppercase;
        color: #ffffff;
}
.partner-child-breakout .content-wrap .single-feature {
      display: flex;
      justify-content: space-between;
      align-items: center;
      margin-top: 40px;
}
.partner-child-breakout .content-wrap .single-feature:first-of-type {
        margin-top: 70px;
}
.partner-child-breakout .content-wrap .single-feature svg {
        width: 100px;
}
.partner-child-breakout .content-wrap .single-feature .feature-description {
        width: calc(100% - 140px);
        display: flex;
        flex-direction: column;
        justify-content: space-between;
}
.partner-child-breakout .content-wrap .single-feature .feature-description .title {
          font-size: 24px;
          font-weight: 700;
}
.partner-child-breakout .content-wrap .single-feature .feature-description .content {
          line-height: 1.3;
          color: #8696ad;
          margin-top: 20px;
}
.partner-child-breakout .content-wrap .single-feature + p {
      margin-top: 60px;
}
.partner-child-breakout:not(.has-bg-image) {
    flex-direction: column;
}
.partner-child-breakout:not(.has-bg-image) .content-wrap {
    margin-right: auto;
    margin-left: auto;
    max-width: 850px;
}
@media only screen and (max-width: 790px) {
.partner-child-breakout:not(.has-bg-image) .content-wrap {
        padding-top: 60px;
}
}
@media only screen and (max-width: 790px) {
.partner-child-breakout .image-wrap {
      right: -15vw;
      left: -15vw;
}
.partner-child-breakout .content-wrap {
      padding: 310px 16px 140px;
      max-width: 100%;
      margin: 0;
      text-align: center;
}
.partner-child-breakout .content-wrap h3 {
        font-size: 30px;
        white-space: normal;
}
.partner-child-breakout .content-wrap p {
        font-size: 16px;
        line-height: 2;
}
.partner-child-breakout .content-wrap .single-feature {
        flex-direction: column;
}
.partner-child-breakout .content-wrap .single-feature:first-of-type {
          margin-top: 55px;
}
.partner-child-breakout .content-wrap .single-feature .feature-description {
          width: 100%;
          margin-top: 30px;
}
.partner-child-breakout .content-wrap .single-feature .feature-description .title {
            font-size: 20px;
            line-height: 1.8;
}
.partner-child-breakout .content-wrap .single-feature .feature-description .content {
            font-size: 16px;
            line-height: 1.8;
}
.partner-child-breakout .content-wrap .single-feature + .single-feature {
          margin-top: 60px;
}
.partner-child-breakout .content-wrap .single-feature + p {
          font-size: 20px;
}
.partner-child-breakout .content-wrap .single-feature ~ p .shortcode-button {
          border-radius: 40px;
          width: 49%;
          box-sizing: border-box;
          margin: 0;
}
}
