.journey-map-wrapper {
  position: absolute;
  bottom: 0;
  right: 0;
  left: 0;
  top: 0;
}
.journey-map-wrapper a {
    outline: none;
}
.journey-map-wrapper .map-svg {
    position: absolute;
    top: 0;
    bottom: 0;
    margin: auto;
    transition: transform 0.4s;
}
.journey-map-wrapper .map-wrap {
    transform-origin: 421px 186px;
    transition: transform 1s;
}
.is-landing .journey-map-wrapper .map-wrap {
    transform: scale(0.4) translateY(100px);
}
.journey-map-wrapper .journey-svg-api.no-hover,
  .journey-map-wrapper .journey-svg-web.no-hover,
  .journey-map-wrapper .journey-svg-mobile.no-hover {
    pointer-events: none;
}
.journey-map-wrapper .intro-wrap {
    opacity: 0;
    transform: none !important;
}
.journey-map-wrapper .intro-wrap.mobile {
      animation: easy-fade 0.6s 0.7s 1 forwards;
}
.journey-map-wrapper .intro-wrap.api {
      animation: easy-fade 0.6s 0.8s 1 forwards;
}
.journey-map-wrapper .intro-wrap.web {
      animation: easy-fade 0.6s 0.9s 1 forwards;
}
