.products-table-item .tooltip-trigger {
  height: 16px;
  width: 16px;
}
.products-table-item .tooltip-trigger {
  margin-right: -18px;
  position: relative;
  margin-left: 2px;
  top: 3px;
}
.not-mobile .products-table-item .tooltip-trigger:hover svg circle,
.not-mobile .products-table-item .tooltip-trigger:focus svg circle {
  fill: #d1d6e0;
}
.products-table-item svg circle {
  transition: fill 250ms;
}
