.glitch-decoration {
  text-transform: uppercase;
}
.glitch-decoration .text-wrap {
    position: relative;
    line-height: 2;
}
.glitch-decoration .r {
    color: #f00;
    animation: glitch1 2s infinite;
}
.glitch-decoration .g {
    color: #0f0;
    animation: glitch1 3s infinite;
}
.glitch-decoration .b {
    color: #00f;
    animation: glitch1 4s infinite;
}
.glitch-decoration .r,
  .glitch-decoration .g,
  .glitch-decoration .b {
    mix-blend-mode: lighten;
}
.glitch-decoration .g,
  .glitch-decoration .b {
    position: absolute;
    bottom: 0;
    right: 0;
    left: 0;
    top: 0;
}
@keyframes glitch1 {
1% {
    transform: scaleY(1);
}
1.5% {
    transform: scaleY(1.02) translateX(-8px) skewX(-1deg);
}
2% {
    transform: scaleY(1) translateX(0) skewX(0deg);
}
51% {
    transform: scaleX(1) scaleY(1) translateY(0px);
}
52% {
    transform: scaleX(1.01) scaleY(0.8) translateX(12px) translateY(20px);
}
53% {
    transform: scaleX(1) scaleY(1) translateX(0) skewX(1deg) translateY(0px);
}
54% {
    transform: scaleX(1) scaleY(1) skewX(0deg);
}
100% {
    transform: scaleY(1);
}
}
