.resources .landing-section .title {
  font-size: 125px;
}
.resources .landing-section .info {
  padding-bottom: 80px;
}
.resources .shared-search-bar {
  margin-top: -190px;
}
.resources .resource-grid {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  grid-gap: 40px 25px;
  padding: 0 30px 80px;
  position: relative;
}
.resources .next-page {
  font-size: 24px;
  font-weight: 700;
  color: #0f61ff;
  text-decoration: none;
  text-transform: uppercase;
  letter-spacing: 1px;
  grid-column-start: 1;
  margin: 30px 0 15px;
  display: block;
}
@media only screen and (max-width: 790px) {
.resources .landing-section {
    padding-top: 90px;
    padding-bottom: 210px;
}
.resources .landing-section .title {
      font-size: 50px;
}
.resources .landing-section .info .wp-content {
      display: none;
}
.resources .shared-search-bar {
    margin: -260px 16px 30px;
    width: calc(100% - 32px);
}
.resources .resource-grid {
    grid-template-columns: 1fr 1fr;
}
}
@media only screen and (max-width: 500px) {
.resources .resource-grid {
    padding: 0 16px 30px;
    display: block;
}
.resources .resource-grid .resource-wrap + .resource-wrap {
      margin-top: 25px;
}
}
