.journey-svg-map .hover-area {
  cursor: pointer;
}
.journey-svg-map image {
  transition: transform 0.4s;
  transform: translate(-150px, 25px) scale(1);
}
.journey-svg-map .map-cursor {
  transition: opacity 0.4s 0.6s;
  display: none;
}
.journey-svg-map .st0 {
  opacity: 0.2;
  fill: none;
  stroke: #ff3655;
  stroke-miterlimit: 10;
}
.journey-svg-map .st1 {
  fill: none;
  stroke: #b2bfd3;
  stroke-miterlimit: 10;
}
.journey-svg-map .st2 {
  fill: #ff3655;
}
.journey-svg-map .st3 {
  fill: none;
  stroke: #ff3655;
  stroke-miterlimit: 10;
}
.is-landing .journey-svg-map .map-cursor {
  opacity: 0;
}
.is-landing .journey-svg-map .hover-area {
  cursor: initial;
}
