main.company-page .company-page-landing .contained {
  padding: 0 30px;
}
@media only screen and (max-width: 1100px) {
main.company-page .company-page-landing .contained {
      display: block;
}
}
@media only screen and (max-width: 1100px) {
main.company-page .company-page-landing .title {
    margin-right: auto;
    margin-left: auto;
    width: 100%;
    text-align: center;
}
}
@media only screen and (max-width: 790px) {
main.company-page .company-page-landing .entry {
