body.page-template-template-products #app {
  background-color: #0b1221;
  min-height: 100vh;
}
.view-products {
  background: linear-gradient(to bottom, #ffffff, #edf4ff);
}
.view-products .fade-enter-active,
  .view-products .fade-leave-active {
    transition: opacity 150ms;
}
.view-products .fade-enter,
  .view-products .fade-leave-to {
    opacity: 0;
}
