body.page-template-template-government #app {
  background-color: #0b1221;
  min-height: 100vh;
}
.government-landing {
  background: #edf4ff;
  padding-bottom: 80px;
  overflow-x: hidden;
}
.government-landing .company-page-landing {
    -webkit-clip-path: polygon(100% 0px, 110% 80%, 19.8% 100%, -10% 92%, 0px 0px);
    clip-path: polygon(100% 0px, 110% 80%, 19.8% 100%, -10% 92%, 0px 0px);
    position: relative;
    z-index: 5;
    padding: 135px 0 155px;
}
.government-landing .company-page-landing .title h2 {
      font-size: 14.5vmin;
}
.government-landing .content-wrap {
    display: grid;
    grid-template-columns: 50% 50%;
    grid-column-gap: 40px;
    transform: translateY(-50px);
    min-height: 60vh;
}
.government-landing .content-wrap .wp-content {
      display: flex;
      flex-direction: column;
      justify-content: flex-end;
      padding-bottom: 40px;
}
.government-landing .content-wrap .wp-content h3 {
        font-size: 60px;
        margin-top: 160px;
}
.government-landing .content-wrap .wp-content p {
        color: #8694af;
        font-size: 20px;
        line-height: 30px;
}
.government-landing .content-wrap .wp-content p strong {
          color: #535e76;
          font-size: 18px;
          letter-spacing: 2px;
          text-transform: uppercase;
          font-weight: 400;
}
.government-landing .content-wrap .image-wrap {
      display: flex;
      flex-direction: column;
      justify-content: center;
      margin-top: 160px;
}
@media only screen and (max-width: 790px) {
.government-landing {
      padding-bottom: 0;
}
.government-landing .company-page-landing .title h2 {
        font-size: 13vmin;
}
.government-landing .content-wrap {
        display: block;
        padding: 60px 20px 0;
}
.government-landing .content-wrap .wp-content {
          padding-bottom: 0;
}
.government-landing .content-wrap .wp-content h3 {
            font-size: 30px;
            margin-bottom: 0;
}
.government-landing .content-wrap .wp-content > *:last-child {
            margin-bottom: 0;
}
}
