.journey-mobile-slideshow {
  background: #002130;
}
.journey-mobile-slideshow .fh-slideshow {
    position: absolute;
    bottom: 0;
    right: 0;
    left: 0;
    top: 0;
    display: flex;
    align-items: flex-start;
    justify-content: center;
    padding-top: 10px;
    box-sizing: border-box;
}
.journey-mobile-slideshow .fh-slideshow .pagination {
      display: flex;
      align-items: center;
      justify-content: center;
}
.journey-mobile-slideshow .fh-slideshow .pagination .pagination-item {
        display: block;
        padding: 20px 7px;
}
.journey-mobile-slideshow .fh-slideshow .pagination .pagination-item .page-button {
          display: flex;
          width: 10px;
          height: 10px;
          background: transparent;
          border: 1px solid #8695ae;
          border-radius: 50%;
          transition: background 0.3s, transform 0.3s;
}
.journey-mobile-slideshow .fh-slideshow .pagination .pagination-item.active .page-button {
          transform: scale(1.6);
          background: #8695ae;
}
.journey-mobile-slideshow .fh-slideshow .nav {
      padding: 18px 25px;
      position: relative;
}
.journey-mobile-slideshow .fh-slideshow .nav svg > g {
        opacity: 1;
        transition: opacity 0.3s;
}
.journey-mobile-slideshow .fh-slideshow.first-slide .nav.prev svg > g {
      opacity: 0.2;
}
.journey-mobile-slideshow .fh-slideshow.last-slide .nav.next svg > g {
      opacity: 0.2;
}
.journey-mobile-slideshow .fh-slideshow .nav.next {
      order: 1;
}
.journey-mobile-slideshow .fh-slideshow .slide {
      padding-top: 90px;
      display: flex;
      flex-direction: column;
      height: 100%;
      box-sizing: border-box;
}
.journey-mobile-slideshow .fh-slideshow .slide .title {
        margin: 0 auto;
        color: #ffffff;
        font-size: 24px;
        font-weight: 500;
        text-align: center;
        position: relative;
        z-index: 5;
}
.journey-mobile-slideshow .fh-slideshow .slide .subtitle {
        font-size: 16px;
        text-align: center;
        font-weight: 400;
        color: #13f7e2;
        text-align: center;
        margin-top: 25px;
        position: relative;
        z-index: 5;
}
.journey-mobile-slideshow .fh-slideshow .slide .wp-content {
        padding: 0 16px;
        font-size: 16px;
        line-height: 1.6;
        color: #8695ae;
        text-align: center;
        position: relative;
        z-index: 5;
}
.journey-mobile-slideshow .fh-slideshow .slide .responsive-image {
        flex: 1;
}
@media only screen and (max-width: 750px) and (orientation: landscape) {
.journey-mobile-slideshow .fh-slideshow .pagination {
      position: relative;
      z-index: 10;
}
.journey-mobile-slideshow .fh-slideshow .text {
      background: rgba(0, 33, 48, 0.8);
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      display: flex;
      flex-direction: column;
      justify-content: center;
      z-index: 5;
}
.journey-mobile-slideshow .fh-slideshow .text .wp-content {
        color: #ffffff;
}
.journey-mobile-slideshow .fh-slideshow .nav {
      z-index: 10;
}
.journey-mobile-slideshow .fh-slideshow .image-wrap {
      position: absolute;
      position: absolute;
      bottom: 0;
      right: 0;
      left: 0;
      top: 0;
}
}
