.products-tooltip {
  box-shadow: 0 12px 34px 0 rgba(0, 25, 62, 0.14);
  background-color: #ffffff;
  box-sizing: border-box;
  margin-left: -190px;
  padding: 40px 50px;
  position: fixed;
  width: 380px;
  z-index: 20;
}
.products-tooltip a {
    transition: border-color 250ms;
    border-bottom: 1px dashed rgba(15, 97, 255, 0.25);
    text-decoration: none;
    display: inline-block;
    padding-bottom: 4px;
    position: relative;
    color: #0f61ff;
}
.not-mobile .products-tooltip a:hover,
    .not-mobile .products-tooltip a:focus {
      border-color: rgba(15, 97, 255, 0.75);
}
.products-tooltip .content {
    color: #8696ad;
}
.products-tooltip .content > *:first-child {
      margin-top: 0;
}
.products-tooltip .content > *:last-child {
      margin-bottom: 0;
}
.products-tooltip .content strong {
      font-size: 20px;
      color: #0b1221;
}
.products-tooltip .triangle {
    transform: translateX(-6px) rotate(45deg);
    transform-origin: center;
    background-color: #ffffff;
    pointer-events: none;
    position: absolute;
    bottom: -13px;
    height: 26px;
    width: 26px;
    left: 50%;
}
.products-tooltip.aligned-right .triangle {
    transform: translateX(-13px) rotate(45deg);
    -webkit-clip-path: polygon(0 0, 0% 100%, 100% 100%);
    clip-path: polygon(0 0, 0% 100%, 100% 100%);
    left: 100%;
}
.products-tooltip.aligned-left {
    margin-left: 0;
}
.products-tooltip.aligned-left .triangle {
    transform: translateX(-13px) rotate(45deg);
    -webkit-clip-path: polygon(100% 0, 0 0, 100% 100%);
    clip-path: polygon(100% 0, 0 0, 100% 100%);
    left: 0;
}
.products-tooltip.tooltip-enter-active, .products-tooltip.tooltip-leave-active {
    transition: opacity 400ms ease, transform 250ms ease;
}
.products-tooltip.tooltip-enter, .products-tooltip.tooltip-leave-to {
    opacity: 0;
}
.products-tooltip.tooltip-enter {
    transform: translateY(30px);
}
@media only screen and (max-width: 790px) {
.products-tooltip {
    padding-right: 16px;
    padding-left: 16px;
    white-space: initial;
    font-size: 14px;
    width: 80%;
}
.products-tooltip strong {
      font-size: 16px;
}
}
