body.page-template-template-contact #app {
  background-color: #0b1221;
  min-height: 100vh;
}
.view-contact .landing-section {
  -webkit-clip-path: polygon(100% 0, 110% 80%, 60% 100%, 0% 100%, 0 0);
  clip-path: polygon(100% 0, 110% 80%, 60% 100%, 0% 100%, 0 0);
  padding-bottom: 130px;
  z-index: 5;
}
.view-contact .landing-section .title {
  font-size: 15vmin;
}
@media only screen and (min-height: 700px) {
.view-contact .landing-section .title {
      font-size: 105px;
}
}
.view-contact .landing-section .info {
  max-width: 975px;
  margin: auto;
}
.view-contact .landing-section .info .wp-content > *:first-child {
    margin-top: 0;
}
.view-contact .primary {
  position: relative;
  margin-top: -8%;
  padding-top: 8%;
  display: flex;
}
.view-contact .primary .right-align {
    margin-left: auto;
    max-width: 460px;
}
.view-contact .primary .form {
    box-sizing: border-box;
    padding: 100px;
    width: 50%;
}
.view-contact .primary .form .mktoForm .mktoFieldWrap {
      float: none;
}
.view-contact .primary .form .mktoFormRow {
      display: flex;
}
.view-contact .primary .form .mktoFormRow > * {
        margin-left: 10px;
        margin-right: 10px;
        flex: 1;
}
.view-contact .primary .form .mktoFormRow > *:first-child {
        margin-left: 0;
}
.view-contact .primary .form .mktoFormRow > *:last-child {
        margin-right: 0;
}
.view-contact .primary .form .mktoFormRow input,
    .view-contact .primary .form .mktoFormRow select,
    .view-contact .primary .form .mktoFormRow textarea {
      box-sizing: border-box;
      width: 100% !important;
      border: none;
      border-bottom: 1px solid #d6ddea;
      font-family: "Graphik";
      border-radius: 0 !important;
      padding-left: 0;
      font-size: 20px;
      text-indent: 0;
      color: #0b1221;
}
.view-contact .primary .form .mktoFormRow input::placeholder,
      .view-contact .primary .form .mktoFormRow select::placeholder,
      .view-contact .primary .form .mktoFormRow textarea::placeholder {
        color: #8695ae;
}
.view-contact .primary .form .mktoFormRow input.mktoInvalid:not(:focus),
      .view-contact .primary .form .mktoFormRow select.mktoInvalid:not(:focus),
      .view-contact .primary .form .mktoFormRow textarea.mktoInvalid:not(:focus) {
        border-bottom: 1px solid #e70040;
}
.view-contact .primary .form .mktoFormRow select {
      background-image: url("images/form-arrow-down.svg");
      background-position: 98% 50%;
      background-repeat: no-repeat;
      color: #8695ae;
      position: relative;
      cursor: pointer;
}
.view-contact .primary .form .mktoButtonRow {
      margin-right: 0 !important;
}
.view-contact .primary .form .mktoButtonRow button {
      transition: border 250ms;
      border-color: #d6ddea !important;
      padding: 18px 20px 20px !important;
      color: #8695ae !important;
      border-width: 2px !important;
      border-radius: 50px;
}
.not-mobile .view-contact .primary .form .mktoButtonRow button:hover,
      .not-mobile .view-contact .primary .form .mktoButtonRow button:focus {
        border-color: #8695ae !important;
        background-color: transparent !important;
}
.view-contact .primary .form .mktoButtonRow button::after {
        transition: transform 250ms;
        background-image: url("images/form-arrow-right.svg");
        content: '';
        position: relative;
        display: inline-block;
        margin-left: 10px;
        height: 15px;
        width: 15px;
        top: 2px;
}
.not-mobile .view-contact .primary .form .mktoButtonRow button:hover::after,
      .not-mobile .view-contact .primary .form .mktoButtonRow button:focus::after {
        transform: translateX(3px);
}
.view-contact .primary .form .mktoOffset,
    .view-contact .primary .form .mktoLabel,
    .view-contact .primary .form .mktoGutter,
    .view-contact .primary .form .mktoClear,
    .view-contact .primary .form .mktoErrorArrowWrap {
      display: none;
}
.view-contact .primary .form .mktoError {
      top: 0 !important;
      right: 0 !important;
      bottom: 0 !important;
      left: 0 !important;
}
.view-contact .primary .form .mktoErrorMsg {
      display: none;
}
.view-contact .primary .form h3 {
    font-size: 60px;
    color: #0b1221;
    margin: 0;
    margin-bottom: 30px;
}
.view-contact .primary .mapbox-container {
    background-color: #f1f1f1;
    position: absolute;
    bottom: 0;
    left: 50%;
    right: 0;
    top: 0;
}
.view-contact section.emails {
  padding: 70px 30px;
  background-color: #0f61ff;
  color: #ffffff;
}
.view-contact section.emails a {
    text-decoration: none;
    font-size: 16px;
    color: #ffffff;
}
.view-contact section.emails .contained {
    grid-template-columns: 1fr 1fr auto;
    grid-column-gap: 25px;
    grid-row-gap: 40px;
    display: grid;
}
.view-contact section.emails .contact-block .purpose {
    font-family: "Crique Grotesk";
    text-transform: uppercase;
    color: rgba(255, 255, 255, 0.55);
    letter-spacing: 2px;
    font-weight: 500;
    font-size: 14px;
}
@media only screen and (max-width: 790px) {
.view-contact {
    overflow: hidden;
}
.view-contact .landing-section {
      -webkit-clip-path: polygon(100% 0, 110% 97%, 55% 100%, -10% 96%, 0 0);
      clip-path: polygon(100% 0, 110% 97%, 55% 100%, -10% 96%, 0 0);
      padding-bottom: 70px;
}
.view-contact .landing-section .title {
        font-size: 60px;
}
.view-contact .primary {
      flex-direction: column;
}
.view-contact .primary .mapbox-container {
        position: relative;
        bottom: initial;
        right: initial;
        left: initial;
        top: initial;
        height: 450px;
}
.view-contact .primary .right-align {
        max-width: initial;
}
.view-contact .primary .form {
        padding-right: 16px;
        padding-left: 16px;
        padding-bottom: 60px;
        padding-top: 60px;
        width: 100%;
}
.view-contact .primary .form .mktoFormRow input,
        .view-contact .primary .form .mktoFormRow select,
        .view-contact .primary .form .mktoFormRow textarea {
          font-size: 18px;
}
.view-contact .primary .form .mktoFormRow {
          flex-direction: column;
}
.view-contact .primary .form .mktoFormRow > * {
          margin-left: 0;
}
.view-contact .primary .form .mktoFormRow select {
          background-size: 12px;
}
.view-contact .primary .form .mktoButtonRow {
          width: 100% !important;
}
.view-contact .primary .form h3 {
        text-align: center;
        font-size: 32px;
}
.view-contact .mobile-mapbox {
      height: 80vh;
}
.view-contact section.emails {
      padding-bottom: 50px;
      padding-top: 50px;
}
.view-contact section.emails .contained {
      grid-template-columns: 1fr;
}
.view-contact .contact-block {
      text-align: center;
}
}
