.front-page-red-team {
  background: #071124;
  color: #ffffff;
  display: flex;
  align-items: center;
  position: relative;
  overflow: hidden;
}
.front-page-red-team .waves-decoration {
    position: absolute;
    bottom: 0;
    right: 0;
    left: 0;
    top: 0;
    background: url("images/patterns/wave.png");
    background-size: 100px;
    opacity: 0.07;
}
.front-page-red-team .image-wrap {
    position: absolute;
    top: 80px;
    right: -40vw;
    bottom: 0;
    left: 60vw;
    margin: auto;
}
.front-page-red-team .image-wrap .background-logo,
    .front-page-red-team .image-wrap .background-logo-glow {
      height: auto;
      width: 100%;
      object-position: center;
      object-fit: contain;
}
.front-page-red-team .image-wrap .background-logo-glow {
      top: 0;
      left: 0;
      position: absolute;
      opacity: 0.5;
      animation: glow 5s infinite;
}
.front-page-red-team .content-wrap {
    max-width: 600px;
    margin: 0 auto 0 12.5vw;
    padding: 80px 20px 200px;
    position: relative;
}
.front-page-red-team .content-wrap .wp-content > * {
      transition: transform 0.4s, opacity 0.4s;
}
.front-page-red-team .content-wrap .wp-content > *:nth-child(1) {
        transition-delay: 0.1s;
}
.front-page-red-team .content-wrap .wp-content > *:nth-child(2) {
        transition-delay: 0.2s;
}
.front-page-red-team .content-wrap .wp-content > *:nth-child(3) {
        transition-delay: 0.3s;
}
.front-page-red-team .content-wrap .wp-content > *:nth-child(4) {
        transition-delay: 0.4s;
}
.front-page-red-team .content-wrap .wp-content > *:nth-child(5) {
        transition-delay: 0.5s;
}
.front-page-red-team .content-wrap .wp-content > *:nth-child(6) {
        transition-delay: 0.6s;
}
.front-page-red-team .content-wrap .wp-content > *:nth-child(7) {
        transition-delay: 0.7s;
}
.front-page-red-team .content-wrap .wp-content > *:nth-child(8) {
        transition-delay: 0.8s;
}
.front-page-red-team .content-wrap .wp-content > *:nth-child(9) {
        transition-delay: 0.9s;
}
.front-page-red-team .content-wrap .wp-content > *:nth-child(10) {
        transition-delay: 1s;
}
.front-page-red-team .content-wrap .wp-content > *:nth-child(11) {
        transition-delay: 1.1s;
}
.front-page-red-team .content-wrap .wp-content > *:nth-child(12) {
        transition-delay: 1.2s;
}
.front-page-red-team .content-wrap .wp-content > *:nth-child(13) {
        transition-delay: 1.3s;
}
.front-page-red-team .content-wrap .wp-content > *:nth-child(14) {
        transition-delay: 1.4s;
}
.front-page-red-team .content-wrap .wp-content > *:nth-child(15) {
        transition-delay: 1.5s;
}
.front-page-red-team .content-wrap .wp-content.below-view:not(.fired-once) > * {
      transform: translateY(50px);
      opacity: 0;
}
.front-page-red-team .content-wrap h5 {
      color: #e70040;
      font-size: 16px;
      margin: 0;
      text-transform: uppercase;
      font-weight: 400;
      letter-spacing: 2px;
}
.front-page-red-team .content-wrap h3 {
      margin: 15px auto 0;
      font-size: 60px;
      white-space: nowrap;
}
.front-page-red-team .content-wrap h3 em {
        font-style: normal;
        color: #777;
}
.front-page-red-team .content-wrap p {
      font-size: 18px;
      margin-top: 30px;
      line-height: 1.4;
      color: #8696ad;
}
.front-page-red-team .content-wrap p em {
        font-style: normal;
        text-transform: uppercase;
        color: #ffffff;
}
.front-page-red-team .content-wrap .single-feature {
      display: flex;
      justify-content: space-between;
      align-items: center;
      margin-top: 40px;
}
.front-page-red-team .content-wrap .single-feature:first-of-type {
        margin-top: 70px;
}
.front-page-red-team .content-wrap .single-feature svg {
        width: 100px;
}
.front-page-red-team .content-wrap .single-feature .feature-description {
        width: calc(100% - 140px);
        display: flex;
        flex-direction: column;
        justify-content: space-between;
}
.front-page-red-team .content-wrap .single-feature .feature-description .title {
          font-size: 24px;
          font-weight: 700;
}
.front-page-red-team .content-wrap .single-feature .feature-description .content {
          line-height: 1.3;
          color: #8696ad;
          margin-top: 20px;
}
.front-page-red-team .content-wrap .single-feature + p {
      margin-top: 60px;
}
@media only screen and (max-width: 790px) {
.front-page-red-team .image-wrap {
      right: -15vw;
      left: -15vw;
}
.front-page-red-team .content-wrap {
      padding: 310px 16px 140px;
      max-width: 100%;
      margin: 0;
      text-align: center;
}
.front-page-red-team .content-wrap h3 {
        font-size: 30px;
        white-space: normal;
}
.front-page-red-team .content-wrap p {
        font-size: 16px;
        line-height: 2;
}
.front-page-red-team .content-wrap .single-feature {
        flex-direction: column;
}
.front-page-red-team .content-wrap .single-feature:first-of-type {
          margin-top: 55px;
}
.front-page-red-team .content-wrap .single-feature .feature-description {
          width: 100%;
          margin-top: 30px;
}
.front-page-red-team .content-wrap .single-feature .feature-description .title {
            font-size: 20px;
            line-height: 1.8;
}
.front-page-red-team .content-wrap .single-feature .feature-description .content {
            font-size: 16px;
            line-height: 1.8;
}
.front-page-red-team .content-wrap .single-feature + .single-feature {
          margin-top: 60px;
}
.front-page-red-team .content-wrap .single-feature + p {
          font-size: 20px;
}
.front-page-red-team .content-wrap .single-feature ~ p .shortcode-button {
          border-radius: 40px;
          width: 49%;
          box-sizing: border-box;
          margin: 0;
}
}
