.front-page .stats {
  background: linear-gradient(to right, #390577, #1c48bf);
  color: #ffffff;
  height: 240px;
  position: relative;
}
.front-page .stats .fh-slideshow {
    position: absolute;
    bottom: 0;
    right: 0;
    left: 0;
    top: 0;
}
.front-page .stats .contained {
    max-width: 1250px;
    margin: auto;
    display: flex;
    padding: 0 30px;
}
.front-page .stats .stat-block {
    display: flex;
    flex-direction: column;
    text-align: center;
    margin: 65px auto 80px;
}
.front-page .stats .stat-block + .stat-block {
      margin-left: 20px;
}
.front-page .stats .stat-block .fixed-width {
      display: inline-block;
}
.front-page .stats .metric {
    font-size: 60px;
    font-family: "Crique Grotesk";
    white-space: nowrap;
    font-weight: 700;
}
.front-page .stats .metric .fixed-width {
      width: 30px;
}
.front-page .stats .description {
    font-size: 20px;
    white-space: nowrap;
}
.front-page .stats .description .fixed-width {
      width: 9px;
}
