.partner-child-breakout-logos {
  padding: 0 30px 140px;
  position: relative;
  width: 100%;
}
.partner-child-breakout-logos .awards-grid {
    grid-template-columns: repeat(5, 1fr);
    list-style-type: none;
    grid-gap: 25px;
    display: grid;
    padding: 0;
    margin: 0;
}
.partner-child-breakout-logos .award-block {
    position: relative;
    text-align: center;
    padding: 30px;
}
.partner-child-breakout-logos .award-block .award-logo {
      margin: auto;
      background-repeat: no-repeat;
      background-position: center;
      background-size: contain;
      padding-bottom: 45%;
      margin-bottom: 10px;
}
.partner-child-breakout-logos .in-view .award-block {
    transition: opacity 600ms, transform 600ms;
    transition-delay: 400ms;
}
.partner-child-breakout-logos .in-view .award-block:nth-child(16n + 1) {
    transition-delay: 475ms;
}
.partner-child-breakout-logos .in-view .award-block:nth-child(16n + 2) {
    transition-delay: 550ms;
}
.partner-child-breakout-logos .in-view .award-block:nth-child(16n + 3) {
    transition-delay: 625ms;
}
.partner-child-breakout-logos .in-view .award-block:nth-child(16n + 4) {
    transition-delay: 700ms;
}
.partner-child-breakout-logos .in-view .award-block:nth-child(16n + 5) {
    transition-delay: 775ms;
}
.partner-child-breakout-logos .in-view .award-block:nth-child(16n + 6) {
    transition-delay: 850ms;
}
.partner-child-breakout-logos .in-view .award-block:nth-child(16n + 7) {
    transition-delay: 925ms;
}
.partner-child-breakout-logos .in-view .award-block:nth-child(16n + 8) {
    transition-delay: 1000ms;
}
.partner-child-breakout-logos .in-view .award-block:nth-child(16n + 9) {
    transition-delay: 1075ms;
}
.partner-child-breakout-logos .in-view .award-block:nth-child(16n + 10) {
    transition-delay: 1150ms;
}
.partner-child-breakout-logos .in-view .award-block:nth-child(16n + 11) {
    transition-delay: 1225ms;
}
.partner-child-breakout-logos .in-view .award-block:nth-child(16n + 12) {
    transition-delay: 1300ms;
}
.partner-child-breakout-logos .in-view .award-block:nth-child(16n + 13) {
    transition-delay: 1375ms;
}
.partner-child-breakout-logos .in-view .award-block:nth-child(16n + 14) {
    transition-delay: 1450ms;
}
.partner-child-breakout-logos .in-view .award-block:nth-child(16n + 15) {
    transition-delay: 1525ms;
}
.partner-child-breakout-logos .in-view .award-block:nth-child(16n + 16) {
    transition-delay: 1600ms;
}
.partner-child-breakout-logos .below-view .award-block {
    transform: translateY(10%);
    opacity: 0;
}
@media only screen and (max-width: 790px) {
.partner-child-breakout-logos {
    padding-right: 16px;
    padding-left: 16px;
    padding-bottom: 60px;
    padding-top: 70px;
}
.partner-child-breakout-logos .awards-grid {
      grid-template-columns: 1fr 1fr;
      grid-gap: 16px;
}
.partner-child-breakout-logos .award-block {
      padding: 20px 15px;
}
.partner-child-breakout-logos .award-block .shared-fading-border {
        -webkit-mask-image: linear-gradient(0deg, black 40%, rgba(0, 0, 0, 0) 120%);
        mask-image: linear-gradient(0deg, black 40%, rgba(0, 0, 0, 0) 120%);
}
}
