.products-grid {
  position: relative;
  padding-top: 130px;
  padding-bottom: 140px;
}
.products-grid .meta {
    text-align: center;
    max-width: 720px;
    margin: auto;
}
.products-grid .meta h5 {
    text-transform: uppercase;
    letter-spacing: 2px;
    font-weight: 500;
    color: #0f61ff;
    font-size: 18px;
}
.products-grid .meta h3 {
    font-size: 60px;
    margin: 0;
}
.products-grid .pattern {
    position: absolute;
    bottom: 0;
    right: 0;
    left: 0;
    top: 0;
    background-image: url("images/patterns/blue-triangles.png");
    background-size: 100px;
}
.products-grid .gradient {
    position: absolute;
    bottom: 0;
    right: 0;
    left: 0;
    top: 0;
    background: linear-gradient(180deg, white 50%, rgba(255, 255, 255, 0) 100%);
}
.products-grid .meta h5 {
    margin-top: 0;
}
.products-grid .grid-wrap {
    text-align: left;
    border: 1px solid #e8ebf0;
    border-radius: 12px;
    background: #e8ebf0;
    overflow: hidden;
    box-shadow: 2px 2px 10px rgba(0, 25, 62, 0.03);
}
.products-grid th,
  .products-grid td {
    background: #ffffff;
    width: 100%;
    height: 100%;
}
.products-grid tr,
  .products-grid table {
    padding: 0;
    vertical-align: initial;
    border: none;
}
.products-grid table {
    padding: 1px 0;
}
.products-grid table.times-row {
      padding: 0 0 1px;
      border-spacing: 0;
      width: 100%;
}
.products-grid table.times-row .products-cell {
        justify-content: center;
}
.products-grid table.times-row .products-cell .text-wrap {
        font-weight: 500;
        color: #535f75;
}
@media only screen and (max-width: 790px) {
.products-grid .meta h3 {
      font-size: 30px;
}
.products-grid .grid-wrap {
      margin-right: 20px;
      margin-left: 20px;
}
}
