.journey-score {
  position: relative;
  display: grid;
  grid-template-columns: 1.1fr 1fr;
  width: 262px;
  height: 91px;
}
.journey-score svg {
    position: absolute;
    bottom: 0;
    right: 0;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
}
.journey-score svg .corners {
      stroke-dasharray: 16px 52px 16px 254px 16px 54px 16px 254px;
      stroke-dashoffset: 6px;
      animation: corner-spin 2s 1 forwards;
}
.journey-score .readout {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    position: relative;
}
.journey-score .readout .number {
      font-size: 32px;
      color: #ffffff;
}
.journey-score .readout .label {
      font-size: 10px;
      color: #64778f;
      letter-spacing: 3px;
      text-align: center;
      text-transform: uppercase;
      margin-top: 6px;
}
@keyframes corner-spin {
0% {
    stroke-dashoffset: 6px;
}
100% {
    stroke-dashoffset: 684px;
}
}
