.svg-pie-chart .pie {
  mask: url(#svg-pie-chart-mask);
}
.svg-pie-chart path {
  fill: transparent;
  stroke-width: 2;
}
.svg-pie-chart path:nth-of-type(1) {
    stroke: #40bf40;
}
.svg-pie-chart path:nth-of-type(2) {
    stroke: #4040bf;
}
.svg-pie-chart path:nth-of-type(3) {
    stroke: #bf4040;
}
