.company-page-values .small-intro {
  color: #0f61ff;
  font-size: 18px;
  font-weight: 400;
  text-align: center;
  text-transform: uppercase;
  letter-spacing: 2px;
  margin-top: 70px;
}
.company-page-values .values-intro {
  text-align: center;
  font-size: 60px;
  margin: 0;
}
.company-page-values .column-wrap {
  padding: 0;
  margin: 0;
  list-style: none;
}
.company-page-values .column-wrap .column {
    display: grid;
    grid-gap: 20px 100px;
    padding-right: 30px;
    padding-left: 30px;
    margin-top: 60px;
    grid-auto-flow: dense;
}
.company-page-values .column-wrap .column.column-natural:nth-child(odd), .company-page-values .column-wrap .column.column-image-right {
      grid-template-columns: 3fr 2fr;
}
@media (max-width: 800px) {
.company-page-values .column-wrap .column.column-natural:nth-child(odd), .company-page-values .column-wrap .column.column-image-right {
          grid-template-columns: 100%;
}
}
.company-page-values .column-wrap .column.column-natural:nth-child(odd) .image, .company-page-values .column-wrap .column.column-image-right .image {
        grid-column-start: 2;
}
@media (max-width: 800px) {
.company-page-values .column-wrap .column.column-natural:nth-child(odd) .image, .company-page-values .column-wrap .column.column-image-right .image {
            grid-column-start: 1;
}
}
.company-page-values .column-wrap .column.column-natural:nth-child(even), .company-page-values .column-wrap .column.column-image-left {
      grid-template-columns: 2fr 3fr;
}
@media (max-width: 800px) {
.company-page-values .column-wrap .column.column-natural:nth-child(even), .company-page-values .column-wrap .column.column-image-left {
          grid-template-columns: 100%;
}
}
.company-page-values .column-wrap .column.column-natural:nth-child(even) .image, .company-page-values .column-wrap .column.column-image-left .image {
        grid-column-start: 1;
}
.company-page-values .column-wrap .column .image {
      width: 100%;
      height: auto;
}
.company-page-values .column-wrap .column .text-wrap h5 {
      margin: 0;
      font-size: 40px;
      font-weight: 400;
}
.company-page-values .column-wrap .column .text-wrap .wp-content {
      color: #8695ae;
}
