.red-team-join-team {
  background: #00111f;
  position: relative;
  color: #ffffff;
}
.red-team-join-team .animation-wrap {
    position: -webkit-sticky;
    position: sticky;
    top: 0;
    box-sizing: border-box;
    text-align: center;
    overflow-x: hidden;
    overflow-y: hidden;
    margin: auto;
}
.red-team-join-team .animation-wrap .content {
      height: 40vh;
      width: 100%;
      position: absolute;
      top: 5vh;
      left: 0;
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: center;
}
.red-team-join-team .animation-wrap .content .slide {
        position: absolute;
        bottom: 0;
        right: 0;
        left: 0;
        top: 0;
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
}
.red-team-join-team .animation-wrap .content .slide:first-child {
          position: relative;
          width: 100%;
}
.red-team-join-team .animation-wrap .content .slide:first-child .wp-content h2 {
            font-size: 60px;
            margin-bottom: 25px;
}
.red-team-join-team .animation-wrap .content .slide:last-child {
          height: 95vh;
}
.red-team-join-team .animation-wrap .content .slide h1 {
          margin: 0;
          text-align: center;
          font-size: 16px;
          text-transform: uppercase;
          letter-spacing: 2px;
          font-weight: 400;
          color: #e9023e;
}
.red-team-join-team .animation-wrap .content .slide .wp-content {
          width: 100%;
}
.red-team-join-team .animation-wrap .content .slide .wp-content h2 {
            font-size: 120px;
            margin: 1.25vh auto 0;
}
.red-team-join-team .animation-wrap .content .slide .wp-content p {
            font-size: 20px;
            font-size: 1.7vh;
            color: #8695ae;
            line-height: 1.4;
            width: 50%;
            margin: auto;
}
.red-team-join-team .animation-wrap .content .slide .wp-content p em {
              color: #e9023e;
              font-style: normal;
              font-weight: 500;
}
.red-team-join-team .animation-wrap .content .slide .wp-content img {
            max-height: 35vh;
            width: auto;
            margin: 25px auto;
}
.red-team-join-team .animation-wrap .content .slide .wp-content .shortcode-button {
            margin: 20px 0;
}
.red-team-join-team .animation-wrap .content .slide + .slide .wp-content p {
          font-size: 24px;
          color: #ffffff;
}
.red-team-join-team .animation-wrap .silhouette-wrap {
      position: absolute;
      top: 45vh;
      right: 5vw;
      bottom: 10vh;
      left: 5vw;
}
.red-team-join-team .animation-wrap .silhouette-wrap .silhouette-field {
        position: absolute;
        bottom: 0;
        right: 0;
        left: 0;
        top: 0;
        width: 100%;
        height: 100%;
        object-fit: contain;
}
.red-team-join-team .animation-wrap .silhouette-wrap.highlighted-figure .selected {
        fill: #ff3655;
}
.red-team-join-team .animation-wrap .silhouette-wrap .selected {
        transition: fill 0.4s;
}
.red-team-join-team .animation-wrap .red-team-pager {
      position: absolute;
      top: 0;
      right: 20px;
      bottom: 0;
      margin: auto;
      transition: opacity 0.4s;
}
.red-team-join-team .animation-wrap .red-team-pager rect {
        transition: opacity 0.4s, transform 0.4s;
}
.red-team-join-team .animation-wrap .icon-mouse {
      position: absolute;
      right: 0;
      bottom: 2vh;
      left: 0;
      margin: auto;
}
.red-team-join-team.slide-0 .red-team-pager {
    opacity: 0;
}
.red-team-join-team.slide-1 .red-team-pager rect:nth-child(1) {
    opacity: 1;
    transform: scale(0.4, 1) translateX(25px);
}
.red-team-join-team.slide-2 .red-team-pager rect:nth-child(2) {
    opacity: 1;
    transform: scale(0.4, 1) translateX(25px);
}
.red-team-join-team.slide-3 .red-team-pager rect:nth-child(3) {
    opacity: 1;
    transform: scale(0.4, 1) translateX(25px);
}
.red-team-join-team.slide-4 .red-team-pager rect:nth-child(4) {
    opacity: 1;
    transform: scale(0.4, 1) translateX(25px);
}
.red-team-join-team.slide-5 .red-team-pager rect:nth-child(5) {
    opacity: 1;
    transform: scale(0.4, 1) translateX(25px);
}
.red-team-join-team.slide-6 .red-team-pager rect:nth-child(6) {
    opacity: 1;
    transform: scale(0.4, 1) translateX(25px);
}
@media only screen and (max-width: 790px) {
.red-team-join-team {
      padding: 0 10px;
}
.red-team-join-team .animation-wrap .silhouette-wrap {
        top: 45vh;
        right: 15px;
        bottom: 15vh;
        left: 15px;
}
.red-team-join-team .animation-wrap .content {
        top: 55px;
        justify-content: initial;
}
.red-team-join-team .animation-wrap .content .slide .wp-content h2,
        .red-team-join-team .animation-wrap .content .slide:first-child .wp-content h2 {
          font-size: 26px;
          margin-bottom: 10px;
}
.red-team-join-team .animation-wrap .content .slide .wp-content p,
        .red-team-join-team .animation-wrap .content .slide:first-child .wp-content p {
          width: 100%;
          font-size: 13px;
}
.red-team-join-team .animation-wrap .content .slide + .slide .wp-content p,
        .red-team-join-team .animation-wrap .content .slide:first-child + .slide .wp-content p {
          font-size: 18px;
}
.red-team-join-team .animation-wrap .red-team-pager {
        display: none;
}
}
