.company-page-benefits.contained {
  padding: 0 30px;
  margin-top: 130px;
}
.company-page-benefits.contained .headline {
    font-size: 60px;
    text-align: center;
    margin: 0;
}
.company-page-benefits.contained .intro {
    font-size: 20px;
    text-align: center;
    color: #8694af;
    margin-top: 45px;
    max-width: 800px;
    margin-right: auto;
    margin-left: auto;
}
.company-page-benefits.contained .icon-grid {
    margin: 0;
    padding: 0;
    list-style: none;
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    grid-gap: 25px;
    margin-top: 65px;
}
@media (max-width: 900px) {
.company-page-benefits.contained .icon-grid {
        grid-template-columns: repeat(2, 1fr);
}
}
.company-page-benefits.contained .icon-grid .icon-single {
      background: linear-gradient(to bottom, #edf4ff, white);
      border-radius: 12px;
      position: relative;
      text-align: center;
      padding: 40px 30px 30px;
}
.company-page-benefits.contained .icon-grid .icon-single:before {
        content: '';
        position: absolute;
        top: -1px;
        left: -1px;
        width: calc(100% + 2px);
        height: calc(100% + 2px);
        background: red;
        z-index: -1;
        border-radius: 12px;
        background: linear-gradient(to bottom, transparent, rgba(15, 249, 228, 0.26));
}
.company-page-benefits.contained .icon-grid .icon-single .image {
        width: 100%;
        max-width: 125px;
        height: auto;
}
