.shared-search-bar {
  position: relative;
  max-width: 800px;
  width: 80%;
  margin: 0 auto 60px;
  display: flex;
  justify-content: space-between;
}
.shared-search-bar .magnifying-glass {
    position: absolute;
    top: 0;
    bottom: 0;
    margin: auto;
}
.shared-search-bar .input {
    width: 60%;
    -webkit-appearance: none;
    background: transparent;
    outline: none;
    border: none;
    border-bottom: 1px solid #3a475a;
    padding: 20px 10px 20px 30px;
    color: #ffffff;
    font-size: 20px;
    border-radius: 0;
}
.shared-search-bar .input ::-webkit-input-placeholder {
      color: #535f75;
}
.shared-search-bar .select {
    width: 30%;
    -webkit-appearance: none;
    background: transparent;
    outline: none;
    border: none;
    border-bottom: 1px solid #3a475a;
    padding: 20px 0;
    color: #ffffff;
    font-size: 20px;
    border-radius: 0;
    cursor: pointer;
}
.shared-search-bar .arrow-down {
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    margin: auto;
}
.shared-search-bar .search-link {
    display: none;
}
@media only screen and (max-width: 790px) {
.shared-search-bar {
      width: 100%;
      flex-wrap: wrap;
}
.shared-search-bar .magnifying-glass {
        top: 23px;
        bottom: initial;
}
.shared-search-bar .input {
        width: 100%;
}
.shared-search-bar .select {
        width: 100%;
        padding-left: 30px;
}
.shared-search-bar .arrow-down {
        top: initial;
        right: initial;
        bottom: 25px;
        left: 2px;
}
}
