.company-page-team-row {
  position: relative;
  padding-bottom: 40%;
  height: 0;
}
.company-page-team-row .flex-wrap {
    position: absolute;
    bottom: 0;
    right: 0;
    left: 0;
    top: 0;
    justify-content: space-between;
    align-items: flex-end;
    display: flex;
}
.company-page-team-row:nth-child(even) .flex-wrap {
    align-items: flex-start;
}
.company-page-team-row .flex-wrap > .column {
    transition: width 400ms cubic-bezier(0.455, 0.03, 0.515, 0.955);
    width: 33.33%;
}
.company-page-team-row .team-block {
    margin-bottom: 30px;
    overflow: hidden;
    display: block;
    width: 100%;
}
.company-page-team-row.remaining .team-block {
    transition: width 400ms cubic-bezier(0.455, 0.03, 0.515, 0.955), width 600ms;
    width: 75%;
}
.company-page-team-row.remaining .column:first-child .team-block {
    margin-left: auto;
}
.company-page-team-row.remaining .team-block:first-child {
    width: 100%;
}
.company-page-team-row.remaining .team-block.is-active {
    width: 100%;
}
@media only screen and (max-width: 790px) {
.company-page-team-row {
    padding-bottom: initial;
    height: initial;
}
.company-page-team-row .column {
      margin: 0 7px;
}
.company-page-team-row .column:first-child {
      margin-left: 0;
}
.company-page-team-row .column:last-child {
      margin-right: 0;
}
.company-page-team-row .flex-wrap {
      position: relative;
}
.company-page-team-row .flex-wrap > .column {
      width: 50%;
}
.company-page-team-row .team-block {
      margin-bottom: 14px;
}
}
