.globe {
  background: #071124;
  background-size: 50px;
  color: #ffffff;
  display: flex;
  align-items: center;
  overflow: hidden;
  position: relative;
}
.globe .grid-background {
    position: absolute;
    bottom: 0;
    right: 0;
    left: 0;
    top: 0;
    background: url("images/patterns/triangles.png");
    background-size: 50px;
    opacity: 0.07;
    pointer-events: none;
}
.globe .front-page-model-globe {
    position: absolute;
    top: 0;
    bottom: 0;
    width: 200%;
    transform: translateX(-50%);
}
@media only screen and (max-width: 790px) {
.globe .front-page-model-globe {
        width: 100%;
        transform: none;
}
}
.globe .content-wrap {
    padding-top: 90px;
    padding-right: 30px;
    padding-bottom: 90px;
    padding-left: 16vw;
    box-sizing: border-box;
}
.globe .content-wrap .link-carousel {
      font-size: 16px;
      height: 2em;
      overflow: visible;
}
.globe .content-wrap .link-carousel .single-link {
        color: #ffffff;
        display: flex;
        align-items: center;
        text-decoration: none;
}
.globe .content-wrap .link-carousel .single-link .new {
          border: 1px solid #e70040;
          text-transform: uppercase;
          font-size: 12px;
          letter-spacing: 2px;
          padding: 6px 8px 7px 10px;
          border-radius: 6px;
          margin-right: 15px;
          font-weight: 500;
}
.globe .content-wrap .link-carousel .single-link .text {
          position: relative;
          bottom: 2px;
}
.globe .content-wrap .link-carousel .single-link .arrow {
          position: relative;
          top: 0px;
          left: 5px;
}
.globe .content-wrap .wp-content {
      margin-top: 50px;
      font-size: 20px;
}
.globe .content-wrap .wp-content h2,
      .globe .content-wrap .wp-content h3 {
        margin: 0;
        font-size: 60px;
        line-height: 1.05;
}
.globe .content-wrap .wp-content h3 + p {
        margin-top: 25px;
}
.globe .content-wrap .wp-content p {
        line-height: 1.4;
        color: #bfc6d2;
        margin: 50px auto;
}
.globe .dotted {
    bottom: 0;
    top: initial;
}
@media only screen and (max-width: 1100px) {
.globe {
      min-height: initial !important;
}
.globe .content-wrap.contained {
        padding-top: 65vw;
}
}
@media only screen and (max-width: 790px) {
.globe .content-wrap {
      max-width: 100%;
      margin: auto auto 30px;
      padding: 40px 16px 0;
      display: flex;
      flex-direction: column;
      justify-content: flex-end;
}
.globe .content-wrap .link-carousel {
        padding: 0 20px;
        position: relative;
}
.globe .content-wrap .link-carousel .slide-wrap {
          width: calc(100% - 45px);
}
.globe .content-wrap .wp-content {
        font-size: 16px;
        text-align: center;
        margin-top: 30px;
}
.globe .content-wrap .wp-content h3 {
          font-size: 30px;
          margin-bottom: 20px;
}
.globe .content-wrap .wp-content p {
          margin: 20px auto;
}
.globe .content-wrap .wp-content > p:last-child {
          position: relative;
          display: flex;
          justify-content: center;
}
.globe .content-wrap .wp-content .shortcode-button {
          padding: 15px 20px;
          white-space: nowrap;
}
.globe .content-wrap .wp-content .shortcode-button.primary {
            padding-top: 18px;
}
}
