body.single header {
  position: absolute;
}
body.single #app {
  min-height: 100vh;
}
.view-single {
  background: linear-gradient(to bottom, #eef5ff, #fff 10%, #fff);
}
.view-single .archive-main-featured:not(.has-image) {
    box-shadow: none;
    filter: drop-shadow(0 0 20px rgba(0, 0, 0, 0.15));
    max-width: 850px;
    margin: auto;
}
.view-single .archive-main-featured:not(.has-image) .content-wrap {
      width: 100%;
      box-sizing: border-box;
}
.view-single .archive-main-featured:not(.has-image) .content-wrap .title {
        display: flex;
        align-items: center;
}
@media only screen and (max-width: 790px) {
.view-single .archive-main-featured:not(.has-image) {
        padding-top: 40px;
        padding-bottom: 0;
}
}
.view-single > .content-wrap {
    padding: 125px 30px 0;
}
.view-single > .content-wrap > .content {
    margin-top: 70px;
    margin-bottom: 100px;
}
.view-single > .content-wrap > .content > *:first-child {
      margin-top: 0;
}
.view-single > .content-wrap > .content > * {
      max-width: 750px;
      margin: 30px auto 0;
}
.view-single > .content-wrap > .content h2 {
      font-size: 40px;
      font-weight: 500;
}
.view-single > .content-wrap > .content ul {
      padding-left: 100px;
      box-sizing: border-box;
      list-style: none;
}
.view-single > .content-wrap > .content li {
      font-size: 20px;
      line-height: 1.6;
      position: relative;
}
.view-single > .content-wrap > .content li + li {
        margin-top: 30px;
}
.view-single > .content-wrap > .content li::before {
        content: '';
        width: 72px;
        height: 1px;
        display: block;
        background-image: url(/wp-content/themes/synack/next/static/images/single-bullet.png);
        background-repeat: no-repeat;
        position: absolute;
        top: 0.8em;
        right: calc(100% + 28px);
}
.view-single > .content-wrap > .content p {
      font-size: 20px;
      line-height: 1.6;
      color: #061023;
}
.view-single > .content-wrap > .content blockquote {
      font-weight: 500;
      font-size: 24px;
      text-align: center;
      padding: 0 100px;
      box-sizing: border-box;
}
.view-single > .content-wrap > .content img {
      width: 100%;
      height: auto;
}
.view-single > .content-wrap > .content .wp-caption {
      width: 100% !important;
}
.view-single > .content-wrap > .content .wp-caption-text {
      margin: 5px 20px 0;
      font-size: 16px;
      color: #8695ae;
      font-style: italic;
}
.view-single > .content-wrap > .content .gallery {
      max-width: 100%;
      display: grid;
      grid-template-columns: repeat(2, 1fr);
}
.view-single > .content-wrap > .content .gallery[data-image-count='3'] {
        grid-template-columns: repeat(3, 1fr);
}
.view-single > .content-wrap > .content .gallery[data-image-count='4'] {
        grid-template-columns: repeat(4, 1fr);
}
@media only screen and (max-width: 790px) {
.view-single > .content-wrap {
      padding: 80px 16px 0;
      margin: 0;
}
.view-single > .content-wrap > .content blockquote {
        padding: 0 10px;
}
.view-single > .content-wrap > .content .gallery {
        display: block;
}
.view-single > .content-wrap .wp-content {
        margin-top: 0;
}
.view-single > .content-wrap .wp-content ul {
          padding-left: 50px;
}
.view-single > .content-wrap .wp-content li::before {
          width: 30px;
          right: calc(100% + 18px);
}
}
