.journey-category {
  background: #052847;
  position: relative;
}
.journey-category .fill {
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    background: #0085a2;
}
.journey-category > .category.label {
    position: relative;
    padding: 10px 10px 8px;
    color: #ffffff;
    display: block;
    text-align: left;
    text-shadow: 1px 1px 1px rgba(0, 0, 0, 0.2);
}
