.products-solutions-graphic {
  overflow: hidden;
  position: relative;
  padding: 110px 30px;
}
.products-solutions-graphic .pattern {
    position: absolute;
    bottom: 0;
    right: 0;
    left: 0;
    top: 0;
    background-image: url("images/patterns/blue-triangles.png");
    background-size: 100px;
}
.products-solutions-graphic .gradient {
    position: absolute;
    bottom: 0;
    right: 0;
    left: 0;
    top: 0;
    background: linear-gradient(180deg, white 50%, rgba(255, 255, 255, 0) 100%);
}
.products-solutions-graphic .meta {
    text-align: center;
    max-width: 720px;
    margin: auto;
}
.products-solutions-graphic .meta h5 {
    text-transform: uppercase;
    letter-spacing: 2px;
    font-weight: 500;
    color: #0f61ff;
    font-size: 18px;
}
.products-solutions-graphic .meta h3 {
    font-size: 60px;
    margin: 0;
}
.products-solutions-graphic .product-link-wrap {
    text-align: center;
    margin-top: 25px;
}
.products-solutions-graphic .product-link-wrap .to-products {
      color: #0f61ff;
      display: inline-block;
      border-color: #0f61ff;
      transition: background 0.3s, color 0.3s;
}
.not-mobile .products-solutions-graphic .product-link-wrap .to-products:not(.primary):hover {
        border-color: #0f61ff;
        background: #0f61ff;
        color: #ffffff;
}
.not-mobile .products-solutions-graphic .product-link-wrap .to-products:not(.primary):hover svg path {
          fill: #ffffff;
}
.products-solutions-graphic .product-link-wrap .to-products svg {
        width: 15px;
        height: 15px;
        position: relative;
        top: 1px;
        left: 15px;
}
.products-solutions-graphic .product-link-wrap .to-products svg path {
          fill: #0f61ff;
          transition: fill 0.3s;
}
.products-solutions-graphic .solutions-step-indicator {
    border-bottom: 1px solid #e7eaef;
    margin-top: 30px;
    padding: 30px;
    display: flex;
}
.products-solutions-graphic .step-indicator {
    flex: 1;
}
.products-solutions-graphic .step-indicator.show-all {
      border-left: 1px solid #e7eaef;
}
.products-solutions-graphic .solutions-visual {
    padding-top: 100px;
    display: flex;
}
.products-solutions-graphic .solutions-visual .graphic,
  .products-solutions-graphic .solutions-visual .information {
    width: 50%;
}
.products-solutions-graphic .solutions-visual .graphic {
    position: relative;
}
.products-solutions-graphic .solutions-visual .information {
    box-sizing: border-box;
    padding-left: 80px;
}
.products-solutions-graphic .solutions-visual .information .step-title {
      text-transform: uppercase;
      letter-spacing: 2px;
      font-weight: 500;
      font-size: 18px;
      margin: 0 0 25px;
}
.products-solutions-graphic .solutions-visual .information .entry {
      min-height: 660px;
      color: #535f75;
      font-size: 20px;
}
.products-solutions-graphic .solutions-visual .information .entry p {
        line-height: 1.3;
}
.products-solutions-graphic .solutions-visual .information .entry h3 {
      font-weight: 500;
      font-size: 40px;
      margin: 30px 0;
      color: #0b1221;
}
.products-solutions-graphic .solutions-visual .information .entry ul {
      list-style-type: none;
      color: rgba(83, 95, 117, 0.75);
      font-size: 18px;
      margin: 30px 0;
      padding: 0;
      margin: 0;
}
.products-solutions-graphic .solutions-visual .information .entry ul li {
      position: relative;
      padding-left: 40px;
}
.products-solutions-graphic .solutions-visual .information .entry ul li::before {
      background-image: url("images/checkmark-blue.svg");
      content: '';
      position: absolute;
      display: block;
      height: 24px;
      width: 24px;
      left: 0;
      top: 2px;
}
.products-solutions-graphic .solutions-visual .information .entry strong {
      font-weight: 500;
      font-size: 18px;
}
.products-solutions-graphic .solutions-visual .information .entry .shortcode-button.primary {
      background-color: #0f61ff;
      color: #ffffff;
}
.products-solutions-graphic .solutions-visual .information .entry .shortcode-button {
      margin: 40px 0;
}
.products-solutions-graphic .solutions-visual .information .entry > *:first-child {
      margin-top: 0;
}
.products-solutions-graphic .solutions-visual .information .entry > *:last-child {
      margin-bottom: 0;
}
@media only screen and (max-width: 790px) {
.products-solutions-graphic {
    padding-right: 0;
    padding-left: 0;
    padding-bottom: 0;
    padding-top: 50px;
}
.products-solutions-graphic .meta {
      padding-right: 16px;
      padding-left: 16px;
}
.products-solutions-graphic .meta h5 {
      font-size: 18px;
}
.products-solutions-graphic .meta h3 {
      font-size: 28px;
      margin: 30px 0;
}
.products-solutions-graphic .solutions-step-indicator {
      padding-right: 10px;
      padding-left: 10px;
      margin-right: auto;
      margin-left: auto;
      padding-top: 10px;
      grid-template-columns: 1fr 1fr;
      grid-gap: 50px 20px;
      display: grid;
      max-width: 300px;
      border: none;
}
.products-solutions-graphic .step-indicator.show-all {
      border: none;
}
.products-solutions-graphic .solutions-visual {
      flex-direction: column;
      margin-right: 16px;
      margin-left: 16px;
      padding-right: 20px;
      padding-left: 20px;
      padding-bottom: 60px;
      border-top: 1px solid #e7eaef;
}
.products-solutions-graphic .solutions-visual .graphic,
      .products-solutions-graphic .solutions-visual .information {
        padding: 0;
        width: 100%;
}
.products-solutions-graphic .solutions-visual .information {
        text-align: center;
        margin-top: 40px;
}
.products-solutions-graphic .solutions-visual .information ul {
          text-align: left;
}
.products-solutions-graphic .solutions-visual .information .shortcode-button {
          box-sizing: border-box;
          margin-bottom: 0;
          width: 100%;
}
}
