.products-cell {
  display: flex;
  align-items: center;
  flex-direction: column;
  text-align: center;
  line-height: 1.3;
  position: relative;
  padding: 15px;
  box-sizing: border-box;
  color: #8696ad;
}
.products-cell:not(.has-text) {
    justify-content: center;
}
.products-cell.has-tooltip {
    padding-right: 40px;
    padding-left: 40px;
}
.products-cell:first-child {
    align-items: flex-start;
    text-align: left;
    justify-content: center;
}
.products-cell img {
    max-width: 24px;
}
.products-cell img[src*='check'] + .text-wrap {
    margin-top: 10px;
}
.products-cell .text-wrap {
    display: flex;
    align-items: center;
    justify-content: flex-start;
    font-size: 14px;
    font-weight: 400;
}
.products-cell .text-wrap img + .text {
      margin-left: 10px;
      font-size: 16px;
      font-weight: 500;
      position: relative;
      top: 2px;
}
.products-cell .checkbox {
    width: 24px;
    height: 24px;
}
.products-cell .tooltip-activator {
    position: absolute;
    top: 0;
    right: 10px;
    bottom: 0;
    margin: auto;
    width: 24px;
    height: 24px;
}
th.products-cell .text-wrap .text {
  font-weight: 500;
}
th.products-cell .text-wrap img[src*='bullet'] + .text {
  font-size: 14px;
  font-weight: 400;
  top: 0;
}
