.careers-openings {
  margin-top: 65px;
}
.careers-openings .selection-wrap {
    background: linear-gradient(to right, #390577, #1c48bf);
    color: #ffffff;
    text-align: center;
    padding: 50px 20px;
}
.careers-openings .selection-wrap h3 {
      margin: 0;
      font-size: 48px;
}
.careers-openings .selection-wrap select {
      margin-top: 35px;
      padding: 20px 15px;
      font-size: 16px;
}
.careers-openings .opening {
    color: #016bff;
    text-align: center;
    font-size: 36px;
    font-weight: 300;
}
.careers-openings .all-openings {
    list-style: none;
    padding: 0 30px;
    margin: 0 auto 20px;
    max-width: 1000px;
}
.careers-openings .all-openings .text-wrap {
      display: flex;
      flex-direction: column;
}
.careers-openings .all-openings .item {
      display: flex;
      justify-content: space-between;
      align-items: center;
      width: 100%;
      color: #0f2749;
      padding: 30px 0;
}
.careers-openings .all-openings .item:first-child {
        padding-top: 0;
}
.careers-openings .all-openings .item + .item {
        border-top: 1px solid #b8c4da;
}
.careers-openings .all-openings .item .title {
        font-size: 20px;
}
.careers-openings .all-openings .item .view-link {
        text-decoration: none;
        padding: 24px 50px 25px;
        font-size: 16px;
        color: #0b1221;
        background: #ffffff;
        border-radius: 45px;
        display: inline-block;
        font-weight: 500;
        transition: border 0.3s;
        white-space: nowrap;
        padding-top: 10px !important;
        padding-bottom: 10px !important;
        text-transform: uppercase;
        font-weight: 400;
        transition: opacity 0.3s;
}
.careers-openings .all-openings .item .view-link:not(.primary) {
          background: #0f61ff;
          color: #ffffff;
          border: 3px solid rgba(15, 97, 255, 0.3);
          padding-bottom: 23px;
          padding-top: 22px;
}
.not-mobile .careers-openings .all-openings .item .view-link:not(.primary):hover,
          .not-mobile .careers-openings .all-openings .item .view-link:not(.primary):focus {
            border-color: transparent;
}
.careers-openings .all-openings .item .view-link + .shortcode-button {
          margin-left: 20px;
}
@media only screen and (max-width: 790px) {
.careers-openings .all-openings .item .view-link {
            font-size: 14px;
            padding-right: 35px;
            padding-left: 35px;
}
}
.careers-openings .all-openings .item .view-link:hover, .careers-openings .all-openings .item .view-link:focus {
          opacity: 0.7;
}
