.archive-main-featured {
  position: relative;
  color: #000000;
  box-shadow: rgba(0, 25, 62, 0.14) 0 12px 34px;
  height: 440px;
}
.archive-main-featured .responsive-image img {
    filter: grayscale(95%);
}
.archive-main-featured .content-wrap {
    position: absolute;
    right: 0;
    bottom: 0;
    width: 85%;
    height: 260px;
    background: #ffffff;
    padding: 40px 40px 30px;
    box-sizing: border-box;
    display: grid;
    grid-template-columns: [date] auto [content] 1fr;
    grid-template-rows: [top] 135px [bottom] auto;
    grid-column-gap: 40px;
    grid-row-gap: 15px;
    clip-path: polygon(101% 0%, 101% 100%, 0% 100%, 0% 30px, 30px 0%);
}
.archive-main-featured .date-number {
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 150px;
    font-size: 13vmin;
    font-weight: 700;
    background: url(/wp-content/themes/synack/next/static/images/patterns/dots-dark.png);
    background-size: 4px;
    -webkit-text-fill-color: transparent;
    text-fill-color: transparent;
    -webkit-background-clip: text;
    background-clip: text;
    transform: translateY(1vh);
}
.archive-main-featured .month-year-wrap {
    grid-area: bottom / date / span 1 / span 1;
    font-size: 18px;
    display: flex;
    align-items: flex-end;
    color: #d0d3d8;
    text-transform: uppercase;
}
.archive-main-featured .month-year-wrap .line {
      flex: 1;
      height: 2px;
      background: #d0d3d8;
      margin: 0 10px;
      transform: translateY(-0.5em);
}
.archive-main-featured .date-wrap {
    grid-row: top / span 2;
    padding-right: 25px;
    border-right: 1px solid #edeff3;
}
.archive-main-featured .title {
    font-size: 40px;
    font-weight: 500;
    margin: 0;
}
.archive-main-featured .author {
    color: #0f61ff;
    font-size: 18px;
    display: flex;
    align-items: flex-end;
}
@media only screen and (max-width: 790px) {
.archive-main-featured {
      text-decoration: none;
      height: initial;
}
.archive-main-featured .content-wrap {
        position: relative;
        bottom: 40px;
        width: 100%;
        box-sizing: border-box;
        padding: 0 0 40px;
        grid-column-gap: 0;
        grid-template-columns: [date] min-content [content] 1fr;
        height: initial;
}
.archive-main-featured .date-number {
        background: url(/wp-content/themes/synack/next/static/images/patterns/dots.png);
        background-size: 4px;
        -webkit-text-fill-color: initial;
        text-fill-color: initial;
        font-size: 60px;
        padding: 10px 20px 0 30px;
        transform: none;
        color: #8695ae;
        justify-content: flex-start;
}
.archive-main-featured .month-year-wrap {
        background: url(/wp-content/themes/synack/next/static/images/patterns/dots.png);
        background-size: 4px;
        grid-area: initial;
        flex-direction: column;
        color: #8695ae;
        align-items: flex-start;
        justify-content: center;
}
.archive-main-featured .month-year-wrap .date-month {
          padding-bottom: 10px;
}
.archive-main-featured .month-year-wrap .line {
          display: none;
}
.archive-main-featured .month-year-wrap .date-year {
          padding-bottom: 5px;
}
.archive-main-featured .title {
        grid-column: 1 / -1;
        padding: 0 30px;
        font-size: 30px;
        font-weight: 700;
}
.archive-main-featured .author {
        padding: 0 30px;
        grid-column: 1 / -1;
}
}
