.journey-mobile-section {
  position: relative;
  min-height: 730px;
  height: 130vw;
}
.journey-mobile-section .landing {
    position: absolute;
    bottom: 0;
    right: 0;
    left: 0;
    top: 0;
    background: linear-gradient(to bottom, #fff, #edf4ff);
    margin: 0 auto 0;
    padding: 60px 16px 0;
    text-align: center;
    z-index: 5;
}
.journey-mobile-section .landing .header-badge {
      margin: 0 auto 10px;
      display: block;
      width: 100px;
      height: auto;
      position: relative;
      z-index: 5;
}
.journey-mobile-section .landing .wp-content {
      position: relative;
      z-index: 5;
}
.journey-mobile-section .landing .wp-content h2 {
        font-size: 60px;
        line-height: 1.1;
        margin: 0 auto 20px;
}
.journey-mobile-section .landing .wp-content h3 {
        font-size: 24px;
        line-height: 1.5;
        margin-bottom: 15px;
}
.journey-mobile-section .landing .wp-content p {
        color: #3e4d66;
        line-height: 1.5;
        margin-top: 0;
        font-size: 20px;
}
.journey-mobile-section .landing .journey-accented-button {
      margin-top: 20px;
      position: relative;
      z-index: 5;
}
.journey-mobile-section .landing .map {
      position: absolute;
      bottom: 0;
      left: 0;
      width: 100%;
      height: auto;
}
