@media (max-width: 800px) {
.career-benefits {
    padding: 0 20px;
}
}
.career-benefits .intro {
  margin-top: 80px;
}
.career-benefits .intro * {
    margin: 0;
    text-align: center;
}
.career-benefits .intro h3 {
    font-size: 52px;
}
@media (max-width: 800px) {
.career-benefits .intro h3 {
        font-size: 36px;
        margin-bottom: 10px;
}
}
.career-benefits .items {
  list-style: none;
  padding: 0;
  margin: 60px 0 0;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}
.career-benefits .items .item {
    text-align: center;
    width: 120px;
    margin: 20px 60px;
}
@media (max-width: 800px) {
.career-benefits .items .item {
        width: 100px;
        margin: 20px;
}
}
.career-benefits .items .item .content {
      margin-top: 10px;
}
