.journey-hover-ui {
  height: 75px;
  display: grid;
  grid-template-columns: 75px 1fr;
  background: #061023;
  width: 400px;
  clip-path: polygon(20px 0, 100% 0%, 100% calc(100% - 20px), calc(100% - 20px) 100%, 0% 100%, 0% 20px);
  pointer-events: none;
  position: fixed;
  transform: translateY(-50%);
}
.journey-hover-ui .icon {
    background: #ff3655;
    width: 75px;
    display: flex;
    align-items: center;
    justify-content: center;
}
.journey-hover-ui .circle-solid {
    stroke-dasharray: 160px;
    stroke-dashoffset: 160px;
    transform: rotateZ(-90deg);
    transform-origin: 26.5px;
}
.journey-hover-ui .content-wrap {
    padding: 10px 30px 10px 20px;
    display: flex;
    flex-direction: column;
    justify-content: center;
}
.journey-hover-ui .title {
    color: #ffffff;
    margin: 0 0 5px;
    font-size: 13px;
    color: #ffffff;
    letter-spacing: 2px;
    text-transform: uppercase;
    font-weight: 400;
}
.journey-hover-ui .wp-content {
    color: #8695ae;
    font-size: 12px;
    letter-spacing: 0;
    line-height: 21px;
}
