.landing-section {
  -webkit-clip-path: polygon(100% 0, 110% 80%, 55% 100%, -10% 92%, 0 0);
  clip-path: polygon(100% 0, 110% 80%, 55% 100%, -10% 92%, 0 0);
  position: relative;
  background: #0b1221;
  overflow: hidden;
  color: #ffffff;
  background: linear-gradient(59deg, #061123 0%, #0f2748 100%);
  padding: 120px 0 130px;
}
.landing-section .shadow {
    transform-origin: bottom left;
    transform: rotate(-25deg);
    position: absolute;
    height: 40px;
    height: 200vh;
    width: 200vw;
    bottom: 0;
    left: 0;
    box-shadow: 0px 0px 50px #000000;
}
.landing-section .shadow-2 {
    bottom: -400px;
}
.landing-section .pattern {
    background-image: url("images/patterns/triangles.png");
    background-size: 50px;
    opacity: 0.07;
    position: absolute;
    bottom: 0;
    right: 0;
    left: 0;
    top: 0;
    bottom: -100vh;
}
.landing-section .info {
    padding: 0 30px;
    position: relative;
    text-align: center;
    max-width: 800px;
    margin: auto;
}
.landing-section .title {
    text-transform: uppercase;
    font-size: 140px;
    margin: 0;
}
.landing-section .entry {
    line-height: 1.4;
    font-size: 20px;
    color: #8696ad;
}
.landing-section .entry a {
      color: #ffffff;
}
.landing-section .load-fade-enter-active,
  .landing-section .load-fade-leave-active {
    transition: opacity 850ms;
}
.landing-section .load-fade-enter,
  .landing-section .load-fade-leave-to {
    opacity: 0;
}
@media only screen and (max-width: 790px) {
.landing-section {
    padding-bottom: 70px;
    padding-top: 100px;
}
.landing-section .info {
      padding-right: 16px;
      padding-left: 16px;
}
.landing-section .title {
      font-size: 50px;
}
.landing-section .entry {
      font-size: 18px;
}
}
