.journey-legend rect {
  transition: stroke 0.3s;
}
.journey-legend text {
  transition: fill 0.3s;
  font-family: 'Crique Grotesk';
}
.journey-legend .score rect {
  stroke: #535f75;
}
.journey-legend .score text {
  fill: #535f75;
}
.journey-legend .vulns rect,
.journey-legend .patches rect {
  fill: transparent;
}
.journey-legend .vulns.active rect {
  stroke: #ff3655;
}
.journey-legend .vulns.active text {
  fill: #ff3655;
}
.journey-legend .patches.active rect {
  stroke: #13f7e2;
}
.journey-legend .patches.active text {
  fill: #13f7e2;
}
