.products-solutions-all {
  padding-bottom: 60px;
  margin-top: 65px;
}
.products-solutions-all .columns {
    box-shadow: 0 4px 8px 0 rgba(0, 25, 62, 0.08);
    border: 1px solid #e8ebf0;
    background-color: #ffffff;
    border-radius: 15px;
    position: relative;
    z-index: 5;
}
.products-solutions-all .header-row {
    position: relative;
    display: flex;
}
.products-solutions-all .header-column {
    border-right: 1px solid #e8ebf0;
    box-sizing: border-box;
    padding: 20px 50px;
    text-align: center;
    width: 25%;
}
.products-solutions-all .header-column:last-child {
      border-right: none;
}
.products-solutions-all .step-label {
    font-weight: 500;
    margin: 14px 0;
    color: #0b1221;
}
.products-solutions-all .step-description {
    font-size: 16px;
    color: #535f75;
}
.products-solutions-all .header-column .circle {
    background-repeat: no-repeat;
    background-position: center;
    background-size: contain;
    margin: auto;
    height: 48px;
    width: 48px;
    margin-bottom: 15px;
}
.products-solutions-all .synack-platform {
    padding-right: 30px;
    padding-left: 30px;
}
.products-solutions-all .synack-platform .platform-row {
      box-shadow: 0 4px 8px 0 rgba(0, 25, 62, 0.08);
      border: 1px solid #e8ebf0;
      background-color: #ffffff;
      align-items: stretch;
      border-radius: 15px;
      display: flex;
      border-top-right-radius: 0;
      border-top-left-radius: 0;
      border-top: 0;
}
.products-solutions-all .synack-platform .col:first-child {
      border-right: 1px solid #e8ebf0;
      align-items: center;
      display: flex;
}
.products-solutions-all .synack-platform .col:last-child {
      font-size: 14px;
      color: #8696ad;
}
.products-solutions-all .synack-platform .col:last-child strong {
        color: #535f75;
}
.products-solutions-all .synack-platform .col {
      padding: 55px 60px;
      flex: 1;
}
.products-solutions-all .synack-platform .circle {
      margin-right: 20px;
      background-repeat: no-repeat;
      background-position: center;
      background-size: contain;
      height: 48px;
      width: 48px;
}
.products-solutions-all .synack-platform .platform-meta {
      flex: 1;
}
.products-solutions-all .synack-platform .step-label {
      font-weight: 500;
      font-size: 20px;
      margin: 0;
}
@media only screen and (max-width: 790px) {
.products-solutions-all {
    -webkit-overflow-scrolling: touch;
    white-space: nowrap;
    overflow-x: auto;
}
.products-solutions-all .columns {
      margin-left: 16px;
      display: inline-block;
      white-space: initial;
      vertical-align: top;
      width: 1100px;
}
.products-solutions-all .synack-platform {
      padding-right: 16px;
      padding-left: 16px;
      display: inline-block;
      white-space: initial;
      vertical-align: top;
      width: 275px;
}
.products-solutions-all .synack-platform .platform-row {
        box-shadow: 0 4px 8px 0 rgba(0, 25, 62, 0.08);
        border: 1px solid #e8ebf0;
        flex-direction: column;
        border-radius: 15px;
}
.products-solutions-all .synack-platform .col:first-child {
        padding: 20px 50px;
        text-align: center;
        display: initial;
        border: none;
        border-bottom: 1px solid #e8ebf0;
}
.products-solutions-all .synack-platform .col:last-child {
        text-align: center;
        padding: 50px 20px;
        line-height: 1.7;
}
.products-solutions-all .synack-platform .col {
        padding-bottom: 20px;
        padding-top: 20px;
        padding-right: 16px;
        padding-left: 16px;
}
.products-solutions-all .synack-platform .circle {
        margin: auto;
        margin-bottom: 15px;
}
}
