.journey-patch-verification {
  position: absolute;
  filter: drop-shadow(0 8px 10px rgba(6, 16, 35, 0.2));
  top: 30vw;
  left: 50vw;
  transform: translate(-50%, -50%);
  pointer-events: all;
}
.journey-patch-verification .indicator {
    position: absolute;
    top: 30px;
    left: 0;
    transform: translateX(calc(-100% + 5px));
    z-index: 5;
}
.journey-patch-verification .grid-wrap {
    background: #ffffff;
    padding: 20px 0;
    max-width: 400px;
    position: relative;
    display: grid;
    grid-template-columns: [left-gutter] 20px [icon] auto [left-column] 1fr [right-column] calc( 50% - 20px) [right-gutter] 20px;
    grid-template-rows: [title] auto [author] auto [content] auto [buttons] auto;
    clip-path: polygon(20px 0, 100% 0, 100% calc(100% - 20px), calc(100% - 20px) 100%, 0 100%, 0 20px);
}
.journey-patch-verification .icon {
    grid-column: icon;
    grid-row: title / content;
    padding-bottom: 15px;
    margin: auto 15px auto 0;
    width: 35px;
    height: 35px;
}
.journey-patch-verification .title {
    margin-top: 0;
    margin-bottom: 10px;
    grid-column: left-column / right-gutter;
    font-size: 13px;
    letter-spacing: 2px;
    text-transform: uppercase;
    font-weight: 400;
}
.journey-patch-verification .author {
    grid-column: left-column / right-gutter;
    color: #8695ae;
    font-size: 14px;
    margin-bottom: 15px;
}
.journey-patch-verification .divider {
    grid-column: left-gutter / -1;
    height: 1px;
    background: #e4e5e9;
}
.journey-patch-verification .wp-content {
    grid-column: icon / right-gutter;
    color: #8695ae;
}
.journey-patch-verification .wp-content h2,
    .journey-patch-verification .wp-content h3 {
      margin: 15px 0 0;
      font-size: 16px;
      font-weight: 400;
}
.journey-patch-verification .wp-content p {
      margin-top: 10px;
      line-height: 1.5;
      font-size: 14px;
}
.journey-patch-verification button {
    border-radius: 22px;
    font-size: 16px;
    padding: 12px 0;
}
.journey-patch-verification .accept,
  .journey-patch-verification .score {
    grid-column: icon / right-column;
    background: #0f61ff;
    color: #ffffff;
    margin-right: 5px;
}
.journey-patch-verification .reject {
    color: #ff3655;
    border: 2px solid #ff3655;
    margin-left: 5px;
}
.journey-patch-verification .score {
    cursor: default;
}
.journey-patch-verification .continue {
    color: #0f61ff;
    border: 2px solid #0f61ff;
    grid-column: icon / right-gutter;
}
