.journey-game {
  position: absolute;
  bottom: 0;
  right: 0;
  left: 0;
  top: 0;
  z-index: 10;
  padding: 75px 30px 0;
  display: grid;
  grid-template-columns: [content] 1fr [legend] 1fr [feed] 1fr;
  grid-column-gap: 20px;
  grid-auto-flow: dense;
  height: 30vh;
  transition: height 1.3s;
  pointer-events: none;
}
.journey-game > *:nth-child(2):not(.fade-leave-active):not(.journey-hover-ui) {
    transition-delay: 0.5s;
}
.journey-game > *:nth-child(3):not(.fade-leave-active):not(.journey-hover-ui) {
    transition-delay: 0.6s;
}
.journey-game .primary-content {
    height: 100%;
    overflow-x: visible;
}
.journey-game .primary-content h2 {
      color: #ffffff;
      font-size: 32px;
      font-weight: 400;
      margin-top: 0;
      margin-bottom: 10px;
}
.journey-game .primary-content h3 {
      color: #13f7e2;
      font-size: 18px;
      font-weight: 400;
      margin-top: 0;
}
.journey-game .primary-content p {
      font-size: 16px;
      line-height: 1.5;
      color: #8695ae;
}
.journey-game .special-action {
    margin-top: 20px;
    margin-left: 5px;
    grid-column: content;
    pointer-events: all;
}
.journey-game .special-action .central-button {
      transition: background 0.4s;
}
.journey-game .special-action .central-button .journey-svg-arrow-replay {
        position: relative;
        top: 2px;
        margin-right: 10px;
}
.journey-game .special-action.show-replay {
      width: 210px;
}
.journey-game .special-action.show-replay .decoration {
        border: 2px solid rgba(255, 255, 255, 0.2);
}
.journey-game .special-action.show-replay .central-button {
        width: 100%;
        background: rgba(15, 97, 255, 0);
        border: 2px solid #4d6178;
        padding: 15px 30px;
}
.journey-game .legend-and-feed {
    grid-column: legend / -1;
    display: flex;
    justify-content: space-between;
}
.journey-game .legend-and-feed > * {
      width: calc(50% - 10px);
}
.journey-game .journey-feed {
    height: 100%;
    overflow-y: auto;
    grid-column: 3 / span 1;
}
.is-report .journey-game {
    height: 60vh;
}
@media only screen and (max-width: 790px) {
.journey-game {
      pointer-events: none;
}
}
