.shared-promise-carousel {
  padding: 120px 30px 60px;
  position: relative;
  margin-top: -120px;
  overflow: hidden;
}
.shared-promise-carousel .stage {
    background-color: #ffffff;
    box-shadow: 0px 12px 34px 0 rgba(0, 25, 62, 0.14);
    margin-top: -120px;
    padding: 60px;
}
.shared-promise-carousel .meta {
    padding-bottom: 50px;
    text-align: center;
    font-size: 20px;
    color: #91a0b6;
    margin: auto;
    line-height: 1.5;
}
.shared-promise-carousel .meta h3 {
      font-size: 60px;
      color: #0b1221;
      margin: 0;
}
.shared-promise-carousel .meta p {
      max-width: 700px;
      margin-right: auto;
      margin-left: auto;
}
.shared-promise-carousel .shadow {
    background-image: url("images/carousel-shadow.png");
    background-size: contain;
    background-repeat: no-repeat;
    background-position: center;
    height: 33px;
    max-width: 740px;
    width: 100%;
    margin: auto;
}
.shared-promise-carousel .carousel {
    line-height: 1.6;
    padding: 30px 120px 25px;
    color: #535f75;
    text-align: center;
    position: relative;
    min-height: 220px;
    font-size: 20px;
    display: flex;
}
.shared-promise-carousel .carousel .active-quotation {
      max-width: 720px;
      margin: auto;
}
.shared-promise-carousel .carousel .svg-quotation-left {
      margin-right: 10px;
      margin-left: -45px;
}
.shared-promise-carousel .carousel .svg-quotation-right {
      vertical-align: bottom;
      position: relative;
      margin-left: 10px;
      margin-right: -45px;
      top: 0.5em;
}
.shared-promise-carousel .carousel .nav {
      position: absolute;
      margin-top: -30px;
      font-size: 0;
      top: 50%;
}
.shared-promise-carousel .carousel .nav.prev {
        left: 0;
}
.shared-promise-carousel .carousel .nav.next {
        right: 0;
}
.shared-promise-carousel .carousel .nav path {
        transition: transform 250ms ease;
}
.not-mobile .shared-promise-carousel .carousel .nav.prev:hover path,
      .not-mobile .shared-promise-carousel .carousel .nav.prev:focus path {
        transform: translateX(-2px);
}
.not-mobile .shared-promise-carousel .carousel .nav.next:hover path,
      .not-mobile .shared-promise-carousel .carousel .nav.next:focus path {
        transform: translateX(2px);
}
.shared-promise-carousel .carousel-enter-active > *,
  .shared-promise-carousel .carousel-leave-active > * {
    transition: opacity 350ms cubic-bezier(0.455, 0.03, 0.515, 0.955), transform 500ms;
}
.shared-promise-carousel .carousel-leave-active .text {
    transition-delay: 200ms;
}
.shared-promise-carousel .carousel-enter > *,
  .shared-promise-carousel .carousel-leave-to > * {
    opacity: 0;
}
.shared-promise-carousel .carousel-enter > * {
    transform: translateY(-10px);
}
@media only screen and (max-width: 790px) {
.shared-promise-carousel {
    padding-right: calc(16px - 10px);
    padding-left: calc(16px - 10px);
    padding-bottom: 40px;
    padding-top: 20px;
}
.shared-promise-carousel .meta {
      padding-bottom: 30px;
}
.shared-promise-carousel .meta h3 {
      font-size: 28px;
}
.shared-promise-carousel .stage {
      margin-top: -20px;
      padding: 25px;
}
.shared-promise-carousel .carousel {
      padding: 25px 0;
      display: block;
}
.shared-promise-carousel .carousel .nav {
        position: static;
        margin-right: 10px;
        margin-left: 10px;
        margin-top: 30px;
}
.shared-promise-carousel .active-quotation .svg-quotation-left,
    .shared-promise-carousel .active-quotation .svg-quotation-right {
      margin: 0;
}
.shared-promise-carousel .active-quotation .text {
      display: block;
}
}
