body.blog #app,
body.archive #app,
body.search #app {
  min-height: 100vh;
}
body.search header {
  position: absolute;
}
main.blog {
  padding: 0 30px;
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  grid-gap: 20px;
}
main.blog .landing-section {
    padding: 0;
    height: 400px;
    position: absolute;
    top: 0;
    right: 0;
    left: 0;
}
main.blog .search-title {
    margin-top: 100px;
    margin-bottom: 20px;
    grid-column: 1 / -1;
    font-size: 48px;
    width: 100%;
}
main.blog .search-title + .shared-search-bar {
      margin-top: 20px;
}
main.blog .shared-search-bar {
    margin-top: 120px;
    margin-bottom: 30px;
    grid-column: 1 / -1;
}
main.blog .archive-featured {
    margin-bottom: 80px;
    position: relative;
    grid-column: 1 / -1;
}
main.blog .archive-category-list {
    height: 100%;
}
main.blog > .links {
    grid-column: 1 / -1;
    list-style: none;
    padding: 0;
    margin: 0 auto 110px;
    width: 100%;
    max-width: 1000px;
    background: #ffffff;
}
main.blog > .links li {
      width: 100%;
}
main.blog > .links li a {
        width: 100%;
}
main.blog > .links li + li {
        margin-top: 30px;
}
main.blog .more-posts {
    margin-top: 30px;
    color: #0f61ff;
    text-transform: uppercase;
    font-size: 20px;
    font-weight: 500;
    text-decoration: none;
    letter-spacing: 1px;
    display: block;
}
main.blog.search-results {
    grid-auto-flow: dense;
    grid-column-gap: 60px;
}
main.blog.search-results > .links {
      grid-column: 2 / span 3;
}
main.blog.search-results .shared-search-bar {
      grid-column: 2 / -1;
      margin: 0;
      width: 100%;
}
main.blog.search-results .shared-search-bar svg circle,
      main.blog.search-results .shared-search-bar svg path {
        stroke: #0f61ff;
}
main.blog.search-results .shared-search-bar .input {
        width: 100%;
        color: #000000;
        border-bottom: 1px solid #dfe6ee;
}
main.blog.search-results .shared-search-bar .input::-webkit-input-placeholder {
          color: #000000;
}
main.blog.search-results .shared-search-bar .select,
      main.blog.search-results .shared-search-bar .arrow-down {
        display: none;
}
main.blog.search-results .archive-category-list {
      grid-row: 2 / span 2;
}
@media only screen and (max-width: 790px) {
main.blog {
      padding: 0 16px;
}
main.blog .archive-featured {
        margin-bottom: 0;
}
main.blog.search-results {
        display: block;
}
main.blog .shared-search-bar {
        margin-top: 80px;
}
main.blog .links {
        margin-bottom: 100px;
}
main.blog .links li:last-child {
          text-align: center;
          margin-top: 85px;
}
}
