.single-related {
  padding: 0 30px 80px;
}
.single-related > .title {
    font-size: 48px;
    margin-bottom: 30px;
}
.single-related .list {
    padding: 0;
    margin: 0;
    list-style: none;
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    grid-column-gap: 25px;
}
.single-related .related-link {
    font-size: 20px;
    font-weight: 700;
    line-height: 1.5;
    text-decoration: none;
    color: #00193e;
    display: flex;
    flex-direction: column;
    height: 100%;
    box-shadow: rgba(0, 25, 62, 0.14) 0 12px 34px;
}
.single-related .related-link .text-wrap {
      padding: 0 30px 35px;
      flex: 1;
      display: flex;
      flex-direction: column;
}
.single-related .related-link .bottom-section {
      display: flex;
      flex-direction: column;
      justify-content: flex-end;
      flex: 1;
}
.single-related .related-link .post-title {
      margin-bottom: 10px;
      line-height: 1.4;
}
.single-related .related-link .author {
      color: #0f61ff;
      font-weight: 400;
      font-size: 16px;
}
.single-related .related-link .date {
      font-size: 14px;
      font-weight: 500;
      text-transform: uppercase;
      color: #d0d3d8;
      letter-spacing: 3px;
}
@media only screen and (max-width: 790px) {
.single-related {
      padding: 0 16px 80px;
}
.single-related .list {
        grid-template-columns: 1fr;
        grid-row-gap: 15px;
}
}
