.red-team-featured-member {
  position: absolute;
  right: 30px;
  bottom: 0;
  left: 30px;
  margin: auto;
  max-width: 1200px;
  height: 85%;
}
.red-team-featured-member .image-wrap {
    transition: opacity 0.4s;
}
.red-team-featured-member .hacker-image {
    position: absolute;
    bottom: 0;
    right: 0;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    object-fit: contain;
    opacity: 0.8;
    object-position: right bottom;
}
.red-team-featured-member .glitch-decoration {
    position: absolute;
    width: 0;
    word-wrap: break-word;
    left: 63%;
    top: 15%;
}
.red-team-featured-member .label-wrap {
    position: absolute;
    top: 10%;
    right: 0;
    font-size: 14px;
    text-transform: uppercase;
    letter-spacing: 4px;
}
.red-team-featured-member .label-wrap .label {
      font-weight: 400;
      font-family: "Graphik";
      box-sizing: border-box;
      position: relative;
      overflow-x: hidden;
}
.red-team-featured-member .label-wrap .label::before {
        content: '';
        position: absolute;
        top: 0;
        bottom: 1px;
        left: 0;
        width: 1px;
        background: #ffffff;
        z-index: 10;
}
.red-team-featured-member .label-wrap .label > span {
        display: inline-block;
        border: 1px solid #ffffff;
        padding: 8px 10px 6px;
}
.red-team-featured-member .label-wrap .label > span:nth-of-type(2) {
          position: relative;
          bottom: 1px;
          animation-delay: 0.8s;
}
.red-team-featured-member .label-wrap .date {
      margin-top: 40px;
}
.red-team-featured-member .label-wrap .date .year {
        letter-spacing: 20px;
}
.red-team-featured-member .label-wrap .date .year span {
          position: relative;
}
.red-team-featured-member .label-wrap .date .year span:nth-child(2) {
            top: 35px;
}
.red-team-featured-member .label-wrap .date .year span:nth-child(3) {
            top: 70px;
}
.red-team-featured-member .label-wrap .date .year span:nth-child(4) {
            top: 105px;
}
.red-team-featured-member .bio {
    width: 50%;
    margin: auto;
    position: absolute;
    top: 0;
    right: 0;
    bottom: 117px;
    background: linear-gradient(#072032, rgba(70, 98, 121, 0));
    border-radius: 10px;
    padding: 30px 30px 75px;
    border: 1px solid rgba(15, 249, 228, 0.26);
    box-sizing: border-box;
}
.red-team-featured-member .bio .bio-scroll-wrap {
      overflow-y: auto;
      -webkit-overflow-scrolling: touch;
      display: grid;
      grid-template-columns: 50% 50%;
      padding: 30px;
      position: absolute;
      bottom: 0;
      right: 0;
      left: 0;
      top: 0;
}
.red-team-featured-member .bio .title {
      margin: 0;
      font-size: 18px;
      display: flex;
      flex-direction: column;
      justify-content: flex-end;
      line-height: 1.5;
}
.red-team-featured-member .bio .name {
      font-weight: 400;
}
.red-team-featured-member .bio .location {
      font-family: "Graphik";
      font-weight: 400;
      opacity: 0.7;
}
.red-team-featured-member .bio .stats {
      background: transparent;
      opacity: 0.7;
      text-transform: uppercase;
      line-height: 1.4;
      font-size: 13px;
      letter-spacing: 3px;
      margin-top: 30px;
      grid-column: 1 / span 1;
}
.red-team-featured-member .bio .image {
      grid-row: 1 / span 2;
      grid-column: 2 / span 1;
      display: flex;
      align-items: center;
}
.red-team-featured-member .bio .biography-copy {
      margin-top: 30px;
      grid-column: 1 / span 2;
      padding-bottom: 75px;
}
.red-team-featured-member .bio .biography-copy .bio-title {
        font-weight: 400;
        margin-bottom: 10px;
        text-transform: uppercase;
}
.red-team-featured-member .bio .biography-copy .wp-content {
        opacity: 0.7;
        line-height: 1.4;
}
.red-team-featured-member .close-bio {
    position: absolute;
    right: 0;
    bottom: -21px;
    left: 0;
    width: 42px;
    height: 42px;
    color: #ffffff;
    margin: auto;
    font-size: 0;
    background: linear-gradient(#072032, rgba(70, 98, 121, 0));
    border-radius: 50%;
    border: 1px solid rgba(15, 249, 228, 0.26);
}
.red-team-featured-member .open-bio {
    position: absolute;
    right: 0;
    bottom: 80px;
    left: 50%;
    width: 120px;
    margin: auto;
    color: #ffffff;
    text-align: center;
    font-size: 14px;
    text-transform: uppercase;
    letter-spacing: 4px;
    transition: opacity 0.4s;
}
.red-team-featured-member .open-bio span {
      display: block;
}
.red-team-featured-member .open-bio .label {
      margin-top: 10px;
      transition: opacity 0.4s;
}
.red-team-featured-member .open-bio .vertical-line {
      transition: transform 0.4s;
      transform-origin: 9.5px 10px;
      transform: rotate(-90deg);
}
.red-team-featured-member.bio-opened .image-wrap {
    opacity: 0.2;
}
.red-team-featured-member.bio-opened .open-bio {
    opacity: 0 !important;
}
.red-team-featured-member.bio-opened .open-bio .vertical-line {
      transform: none;
}
@media only screen and (max-width: 790px) {
.red-team-featured-member {
      position: relative;
      top: 0;
      right: 0;
      bottom: 0;
      left: 0;
      min-height: 400px;
      width: 100%;
      padding-bottom: 90px;
}
.red-team-featured-member .hacker-image {
        object-fit: contain;
        object-position: center;
}
.red-team-featured-member .glitch-decoration {
        top: 60px;
        left: 50px;
}
.red-team-featured-member .label-wrap {
        top: 0;
        right: 15px;
}
.red-team-featured-member .open-bio {
        bottom: 30px;
        left: 0;
}
.red-team-featured-member .bio {
        box-sizing: border-box;
        width: calc(100% - 32px);
        max-width: 500px;
        position: static;
        padding: 10px;
}
.red-team-featured-member .bio .biography-copy {
          padding-bottom: 40px;
}
.red-team-featured-member .bio .bio-scroll-wrap {
          position: static;
          padding: 0;
}
.red-team-featured-member .bio .bio-scroll-wrap .image {
            grid-row: 1 / span 1;
            grid-column: 1 / span 2;
            height: 200px;
            margin: auto;
            overflow: hidden;
}
.red-team-featured-member .bio .bio-scroll-wrap .image .image-sizer {
              position: absolute;
              bottom: 0;
              right: 0;
              left: 0;
              top: 0;
              padding: 0 !important;
}
.red-team-featured-member .bio .bio-scroll-wrap .title {
            grid-row: 2 / span 1;
            grid-column: 1 / span 2;
            margin-top: 20px;
}
.red-team-featured-member .bio .bio-scroll-wrap .stats {
            grid-row: 3 / span 1;
            grid-column: 1 / span 2;
            margin-top: 20px;
}
.red-team-featured-member .bio .bio-scroll-wrap .biography-copy {
            grid-row: 4 / span 1;
            grid-column: 1 / span 2;
}
.red-team-featured-member .bio .close-bio {
          bottom: 68px;
}
.red-team-featured-member.bio-opened .image-wrap {
        opacity: 0;
}
}
