.page-layout-content > .content {
  margin-top: 70px;
  margin-bottom: 100px;
}
.page-layout-content > .content > *:first-child {
    margin-top: 0;
}
.page-layout-content > .content > * {
    max-width: 750px;
    margin: 30px auto 0;
}
.page-layout-content > .content h2 {
    font-size: 40px;
    font-weight: 500;
}
.page-layout-content > .content ul {
    padding-left: 100px;
    box-sizing: border-box;
    list-style: none;
}
.page-layout-content > .content li {
    font-size: 20px;
    line-height: 1.6;
    position: relative;
}
.page-layout-content > .content li + li {
      margin-top: 30px;
}
.page-layout-content > .content li::before {
      content: '';
      width: 72px;
      height: 1px;
      display: block;
      background-image: url(/wp-content/themes/synack/next/static/images/single-bullet.png);
      background-repeat: no-repeat;
      position: absolute;
      top: 0.8em;
      right: calc(100% + 28px);
}
.page-layout-content > .content p {
    font-size: 20px;
    line-height: 1.6;
    color: #061023;
}
.page-layout-content > .content blockquote {
    font-weight: 500;
    font-size: 24px;
    text-align: center;
    padding: 0 100px;
    box-sizing: border-box;
}
.page-layout-content > .content img {
    width: 100%;
    height: auto;
}
.page-layout-content > .content .wp-caption {
    width: 100% !important;
}
.page-layout-content > .content .wp-caption-text {
    margin: 5px 20px 0;
    font-size: 16px;
    color: #8695ae;
    font-style: italic;
}
.page-layout-content > .content .gallery {
    max-width: 100%;
    display: grid;
    grid-template-columns: repeat(2, 1fr);
}
.page-layout-content > .content .gallery[data-image-count='3'] {
      grid-template-columns: repeat(3, 1fr);
}
.page-layout-content > .content .gallery[data-image-count='4'] {
      grid-template-columns: repeat(4, 1fr);
}
@media only screen and (max-width: 790px) {
.page-layout-content {
    padding: 80px 16px 0;
    margin: 0;
}
.page-layout-content > .content blockquote {
      padding: 0 10px;
}
.page-layout-content > .content .gallery {
      display: block;
}
.page-layout-content .wp-content {
      margin-top: 0;
}
.page-layout-content .wp-content ul {
        padding-left: 50px;
}
.page-layout-content .wp-content li::before {
        width: 30px;
        right: calc(100% + 18px);
}
}
