.company-page-openings-intro {
  padding: 55px 30px 60px;
  background: linear-gradient(to right, #390577, #1c48bf);
  color: #ffffff;
  text-align: center;
  margin-top: 100px;
}
.company-page-openings-intro .headline {
    font-size: 60px;
    margin: 0;
}
.company-page-openings-intro .content {
    font-size: 20px;
    font-weight: 700;
    line-height: 1.5;
    margin-bottom: 0;
    max-width: 800px;
    margin-right: auto;
    margin-left: auto;
}
