.products-header {
  display: grid;
  grid-template-columns: repeat(5, 1fr);
  list-style: none;
  padding: 85px 0 30px;
  margin: 0 2px;
  background: #ffffff;
  text-align: center;
  position: sticky;
  top: 0;
  z-index: 5;
  border-radius: 12px;
}
.products-header img {
    width: 48px;
    height: 48px;
    margin-left: auto;
    margin-right: auto;
}
.products-header h5 {
    font-weight: bold;
    font-size: 20px;
    margin: 10px auto 15px;
}
@media only screen and (max-width: 790px) {
.products-header {
      grid-template-columns: repeat(2, 1fr);
      grid-row-gap: 45px;
      grid-column-gap: 15px;
      padding-top: 45px;
      margin-right: 20px;
      margin-left: 20px;
      position: static;
}
.products-header .empty {
        display: none;
}
.products-header .description {
        font-size: 14px;
}
.products-header .icon:not(.selected) {
        filter: grayscale(100%);
}
.not-mobile .products-header button:hover .icon,
      .not-mobile .products-header button:focus .icon {
        filter: none;
}
}
