.products-pillars-vector image {
  transition: transform 600ms cubic-bezier(0.455, 0.03, 0.515, 0.955), opacity 300ms linear;
}
.products-pillars-vector .activestate {
  opacity: 0.15;
}
.products-pillars-vector.hydra-active .hydra.activestate,
.products-pillars-vector.hydra-active .hydra-front.activestate {
  opacity: 1;
}
.products-pillars-vector.hydra-active .hydra,
.products-pillars-vector.hydra-active .hydra-front {
  transform: translateY(-600px);
}
.products-pillars-vector.lp-active .lp.activestate {
  opacity: 1;
}
.products-pillars-vector.lp-active .lp {
  transform: translateY(-600px);
}
.products-pillars-vector.apollo-active .apollo.activestate {
  opacity: 1;
}
.products-pillars-vector.apollo-active .apollo {
  transform: translateY(-600px);
}
