.products-solutions-legend circle {
  transition: fill 250ms ease;
}
.products-solutions-legend .outline {
  fill: none;
}
.products-solutions-legend line {
  transition: stroke 250ms ease;
}
.products-solutions-legend.active-0 circle.fill {
  fill: #ffad39;
}
.products-solutions-legend.active-0 circle.outline {
  stroke: #ffad39;
}
.products-solutions-legend.active-0 line {
  stroke: #d27b00;
}
.products-solutions-legend.active-1 circle {
  fill: #48d895;
}
.products-solutions-legend.active-1 line {
  stroke: #219961;
}
.products-solutions-legend.active-2 circle {
  fill: #3dcbdc;
}
.products-solutions-legend.active-2 line {
  stroke: #1b8a98;
}
.products-solutions-legend.active-3 circle {
  fill: #0f61ff;
}
.products-solutions-legend.active-3 line {
  stroke: #0039a8;
}
.products-solutions-legend.active-4 circle {
  fill: #b39cff;
}
.products-solutions-legend.active-4 line {
  stroke: #6536ff;
}
.products-solutions-legend.active-5 circle {
  fill: #e70040;
}
.products-solutions-legend.active-5 line {
  stroke: #810024;
}
